.thanks__caption-wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.thanks__btn-wrapper {
  display: flex;
  align-items: center;
  margin: 20px 0;
}

@media (min-width: 0) and (max-width: 499px) {
  .thanks__btn-wrapper {
    flex-direction: column;
    align-items: flex-start;
  }
}

.thanks__btn-wrapper .thanks__link {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1rem;
  color: #0063b0;
  line-height: 1.25rem;
  font-style: normal;
}

.thanks__btn-wrapper .thanks__link:hover {
  color: #4d92c8;
}

.thanks__btn-wrapper .button {
  margin: 0 20px 0 100px;
}

@media (min-width: 0) and (max-width: 499px) {
  .thanks__btn-wrapper .button {
    margin: 0 0 10px 0;
  }
}

.thanks__ok {
  position: relative;
  width: 80px;
  height: 80px;
  min-width: 80px;
  border: 8px solid #e6e6e6;
  border-radius: 50%;
  margin-right: 20px;
}

@media (min-width: 0) and (max-width: 499px) {
  .thanks__ok {
    width: 40px;
    height: 40px;
    min-width: 40px;
    border-width: 4px;
  }
}

.thanks__ok:after {
  content: "";
  position: absolute;
  top: 17px;
  right: auto;
  bottom: auto;
  left: 35px;
  width: 8px;
  height: 30px;
  transform: rotate(45deg);
  background-color: #5cb85c;
  border-radius: 10px;
}

@media (min-width: 0) and (max-width: 499px) {
  .thanks__ok:after {
    left: 18px;
    top: 8px;
    width: 4px;
    height: 15px;
  }
}

.thanks__ok:before {
  content: "";
  position: absolute;
  top: 25px;
  right: auto;
  bottom: auto;
  left: 20px;
  width: 8px;
  height: 23px;
  transform: rotate(-45deg);
  background-color: #5cb85c;
  border-radius: 10px;
}

@media (min-width: 0) and (max-width: 499px) {
  .thanks__ok:before {
    left: 9px;
    top: 12px;
    width: 4px;
    height: 12px;
  }
}

.thanks__caption {
  margin: 0;
  padding: 0;
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: bold;
  font-size: 1.5rem;
  color: #000;
  line-height: 2.5rem;
  font-style: normal;
}

@media (min-width: 0) and (max-width: 499px) {
  .thanks__caption {
    line-height: 1.25rem;
  }
}

.thanks__info {
  margin-left: 100px;
}

@media (min-width: 0) and (max-width: 499px) {
  .thanks__info {
    margin-left: 0;
  }
}

.thanks__info .thanks__info-text {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 300;
  font-size: 1.25rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
  margin-bottom: 20px;
}

.thanks__info .thanks__info-phone-wrapper,
.thanks__info .thanks__info-email-wrapper {
  display: flex;
  margin-bottom: 20px;
}

.thanks__info .thanks__info-phone-wrapper .thanks__info-phone-link,
.thanks__info .thanks__info-phone-wrapper .thanks__info-email-text,
.thanks__info .thanks__info-email-wrapper .thanks__info-phone-link,
.thanks__info .thanks__info-email-wrapper .thanks__info-email-text {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
}

.thanks__info .thanks__info-phone-wrapper .thanks__info-phone-link,
.thanks__info .thanks__info-email-wrapper .thanks__info-phone-link {
  color: #0063b0;
}

.thanks__info .thanks__info-phone-wrapper .thanks__info-phone-link:hover,
.thanks__info .thanks__info-email-wrapper .thanks__info-phone-link:hover {
  color: #4d92c8;
}

.thanks__info .thanks__info-phone-wrapper .thanks__info-phone,
.thanks__info .thanks__info-phone-wrapper .thanks__info-email,
.thanks__info .thanks__info-email-wrapper .thanks__info-phone,
.thanks__info .thanks__info-email-wrapper .thanks__info-email {
  vertical-align: top;
  display: inline-block;
  min-width: 200px;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: bold;
  font-size: 1rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
}

@media (min-width: 0) and (max-width: 499px) {
  .thanks__info .thanks__info-phone-wrapper .thanks__info-phone,
  .thanks__info .thanks__info-phone-wrapper .thanks__info-email,
  .thanks__info .thanks__info-email-wrapper .thanks__info-phone,
  .thanks__info .thanks__info-email-wrapper .thanks__info-email {
    min-width: 180px;
  }
}




