.label {
  display: inline;
  padding: 5px 10px;
  font-size: 75%;
  font-weight: 700;
  line-height: 1;
  color: #fff;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: 4px;
}

.label--default {
  background-color: #777777;
}

.label--primary {
  background-color: #0063b0;
}

.label--success {
  background-color: #5cb85c;
}

.label--danger {
  background-color: #e90e3f;
}

.label--warning {
  background-color: #f0ad4e;
}

.label--info {
  background-color: #9dd6fd;
}




