.button-group {
  position: relative;
  display: flex;
  & .button {
    border-radius: 0;
    & + .button {
      margin-left: -1px; }
    &:last-child {
      border-bottom-right-radius: 4px;
      border-top-right-radius: 4px; }
    &:first-child {
      border-bottom-left-radius: 4px;
      border-top-left-radius: 4px; } } }


.button-group-justified .button {
  width: 100%; }

.button-group-vertical {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &.button-group-block {
    align-items: stretch; }
  & .button {
    margin: 0;
    border-radius: 0;
    & + .button {
      margin-top: -1px; }
    &:last-child {
      border-bottom-right-radius: 4px;
      border-bottom-left-radius: 4px; }
    &:first-child {
      border-top-right-radius: 4px;
      border-top-left-radius: 4px; } } }

.button-group {
  &-lg .button {
    padding: 10px 20px;
    font-size: 20px; }
  &-sm .button {
    padding: 5px 10px;
    font-size: 15px; }
  &-xs .button {
    padding: 3px 6px;
    font-size: 14px; } }




