.nav {
  display: inline-block;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 20px;
  list-style: none;
}

.nav > li {
  position: relative;
  display: block;
  float: left;
  border: 1px solid #97c3e0;
  border-radius: 0;
}

.nav > li.disabled {
  background: rgba(119, 119, 119, 0.4);
  cursor: no-drop;
}

.nav > li:first-child {
  border-bottom-left-radius: 4px;
  border-top-left-radius: 4px;
}

.nav > li:last-child {
  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px;
}

.nav > li + li {
  margin-left: -1px;
}

.nav > li a {
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: bold;
  font-size: 0.875rem;
  color: #555555;
  line-height: 1.25rem;
  font-style: normal;
  padding: 9px 18px;
  border-radius: 0;
  border: none;
  position: relative;
  display: block;
  text-decoration: none;
}

.nav > li a:hover,
.nav > li a:focus {
  background-color: rgba(151, 195, 224, 0.4);
  color: black;
}

.nav > li.active.disabled a {
  background: rgba(107, 169, 210, 0.58);
}

.nav > li.active a {
  background-color: #6ba9d2;
  color: white;
  cursor: default;
}

.nav > li.active a:hover,
.nav > li.active a:focus {
  background-color: #6ba9d2;
  color: white;
}




