@import "../../sass/mixin";
@import "../../sass/vars";
@import "../../sass/media";

.banner-logo {
  display: block;
  margin: 0!important;
  text-decoration: none!important;
  & .banner-logo__prefix {
    $banner-logo__prefix_ff: $general-ff-ekibastuz;
    $banner-logo__prefix_weight: $general-font-weight-bold;
    $banner-logo__prefix_size: $general-font-size-normal;
    $banner-logo__prefix_color: color('primary');

    display: flex;
    margin: 0;
    @include font($banner-logo__prefix_ff, $banner-logo__prefix_weight, $banner-logo__prefix_size, $banner-logo__prefix_color);
    text-transform: uppercase;

    & .banner-logo__suffix {
      $banner-logo__suffix_color: color('accent');

      margin-left: 5px;
      text-transform: uppercase;
      color: $banner-logo__suffix_color; } } }



