@import "../../sass/mixin";
@import "../../sass/vars";
@import "../../sass/media";
@import "../../sass/inputs";


$firstStep_padding-bottom: 40px;
$firstStep_padding-left: 60px;
.firstStep {
  margin: 0;
  padding: 0 0 $firstStep_padding-bottom $firstStep_padding-left;
  position: relative;
  display: flex;
  flex-wrap: wrap;

  $firstStep_before_width: 3px;
  $firstStep_before_height: 100%;
  $firstStep_before_top: 0;
  $firstStep_before_left: 20px;
  &:before {
    @include pseudo($firstStep_before_width, $firstStep_before_height, $firstStep_before_top, auto, auto, $firstStep_before_left);
    border-radius: 4px; }

  $firstStep__caption_width: 100%;
  $firstStep__caption_font-size: $general-font-size-normal;

  $firstStep__caption-span_width: 30px;
  $firstStep__caption-span_height: $firstStep__caption-span_width;
  $firstStep__caption-span_top: 0;
  $firstStep__caption-span_left: -53px;
  $firstStep__caption-span_bg-color: white;
  $firstStep__caption-span_outline-color: white;
  $firstStep__caption-span_outline-width: 5px;
  $firstStep__caption-span_border-color: #f16d00;
  $firstStep__caption-span_border-width: 8px;
  $firstStep__caption-span_border-radius: 50%;

  $firstStep__list-tip-item-margin-right: 60px;
  .firstStep__caption {
    padding-top: 5px;
    list-style: none;
    position: relative;
    width: $firstStep__caption_width;
    margin: 0 0 20px 0;
    @include font($general-ff-helvetica, $general-font-weight-bold, $firstStep__caption_font-size);
    & span {
      display: block;
      @include pseudo($firstStep__caption-span_width, $firstStep__caption-span_height, $firstStep__caption-span_top, auto, auto, $firstStep__caption-span_left);
      border: $firstStep__caption-span_border-width solid;
      border-radius: $firstStep__caption-span_border-radius;
      outline: $firstStep__caption-span_outline-width solid $firstStep__caption-span_outline-color;
      background: $firstStep__caption-span_bg-color;
      z-index: 3; } }

  $firstStep__input-margin-right: 10px;
  .firstStep__radio-wrapper {
    margin-right: 40px;
    margin-bottom: 20px;
    width: auto!important;
    display: inline-block!important;
    &.field-radio-group {
      width: auto;
      margin-top: 0; }
    & .firstStep__radio {
      @include font();
      margin-right: $firstStep__input-margin-right; } } }

$color-theme: (transportAndDriving: $transportAndDriving, primary: color('primary', 'light'));
@each $name, $color in $color-theme {
  .firstStep.#{$name} {
    &:before {
      background-color: #{$color}; }
    & .firstStep__caption span {
      border-color: #{$color}; } } }



