@mixin grid($state: fixed) {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  margin: {
    left: auto;
    right: auto; }

  @if ($state == fixed) {
    @include media-lg() {
      width: $lg - $margin; }
    @include media-md() {
      width: 100%; } }

  @else if ($state == fluid) {
    width: 100%; } }



