.button-wrapper {
  display: block;
}

.button-wrapper__left {
  text-align: left;
}

.button-wrapper__center {
  text-align: center;
}

.button-wrapper__right {
  text-align: right;
}

.button {
  position: relative;
  vertical-align: top;
  display: inline-block;
  border: 1px solid transparent;
  border-radius: 4px;
  padding: 12px 15px;
  margin: 5px 0;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
  transition: all 0.4s ease;
  cursor: pointer;
  white-space: nowrap;
  touch-action: manipulation;
}

.button:focus {
  outline: none;
}

.button[disabled] {
  cursor: not-allowed;
  opacity: .65;
  filter: alpha(opacity=65);
  box-shadow: none;
}

.button.button-block {
  display: block;
  width: 100%;
}

.button.button-default {
  color: #333333;
  background-color: white;
  border-color: rgba(119, 119, 119, 0.4);
}

.button.button-default:hover,
.button.button-default:focus {
  background-color: #e6e6e6;
}

.button.button-default:active {
  background-color: #cccccc;
}

.button.button-default.active {
  background-color: #cccccc;
}

.button.button-primary {
  color: white;
  background-color: #0063b0;
  border-color: rgba(0, 69, 123, 0.6);
}

.button.button-primary:hover,
.button.button-primary:focus {
  background-color: #00599e;
}

.button.button-primary:active {
  background-color: #004f8d;
}

.button.button-primary.active {
  background-color: #004f8d;
}

.button.button-info {
  color: white;
  background-color: #9dd6fd;
  border-color: rgba(110, 150, 177, 0.6);
}

.button.button-info:hover,
.button.button-info:focus {
  background-color: #8dc1e4;
}

.button.button-info:active {
  background-color: #7eabca;
}

.button.button-info.active {
  background-color: #7eabca;
}

.button.button-success {
  color: white;
  background-color: #5cb85c;
  border-color: rgba(64, 129, 64, 0.6);
}

.button.button-success:hover,
.button.button-success:focus {
  background-color: #53a653;
}

.button.button-success:active {
  background-color: #4a934a;
}

.button.button-success.active {
  background-color: #4a934a;
}

.button.button-warning {
  color: white;
  background-color: #f0ad4e;
  border-color: rgba(168, 121, 55, 0.6);
}

.button.button-warning:hover,
.button.button-warning:focus {
  background-color: #d89c46;
}

.button.button-warning:active {
  background-color: #c08a3e;
}

.button.button-warning.active {
  background-color: #c08a3e;
}

.button.button-danger {
  color: white;
  background-color: #e90e3f;
  border-color: rgba(163, 10, 44, 0.6);
}

.button.button-danger:hover,
.button.button-danger:focus {
  background-color: #d20d39;
}

.button.button-danger:active {
  background-color: #ba0b32;
}

.button.button-danger.active {
  background-color: #ba0b32;
}

.button.button-link {
  color: #0063b0;
  background-color: transparent;
  border-color: transparent;
  border-radius: 0;
  box-shadow: none;
}

.button.button-link:hover,
.button.button-link:focus {
  background-color: transparent;
  color: #00599e;
  text-decoration: underline;
}

.button.button-link:active {
  background-color: transparent;
  color: #004f8d;
  text-decoration: underline;
}

.button.button-link.active {
  background-color: transparent;
  color: #004f8d;
  text-decoration: underline;
}

.button.button-lg {
  font-size: 1.125rem;
  padding: 16px 30px;
}

.button.button-sm {
  font-size: 0.9375rem;
  padding: 5px 10px;
}

.button.button-xs {
  font-size: 0.875rem;
  padding: 3px 6px;
}

.button.button-arrow-right {
  padding: 8px 25px 8px 15px;
  color: white;
  background-color: #0063b0;
}

.button.button-arrow-right:before {
  content: "";
  position: absolute;
  top: 50%;
  right: 10px;
  bottom: auto;
  left: auto;
  width: 10px;
  height: 10px;
  margin-top: -5px;
  border-bottom: 2px solid white;
  border-right: 2px solid white;
  border-radius: 2px;
  transform: rotate(-45deg);
}

.button.button-arrow-right:hover,
.button.button-arrow-right:focus {
  background-color: #00599e;
}

.button.button-arrow-right:active {
  background-color: #004f8d;
}

.button.button-arrow-right.active {
  background-color: #004f8d;
}

.button.button-arrow-right.button-lg {
  padding: 16px 44px 16px 30px;
}

.button.button-arrow-right.button-lg:before {
  right: 18px;
  margin-top: -6px;
  width: 12px;
  height: 12px;
}

.button.button-arrow-right.button-sm {
  padding: 5px 20px 5px 10px;
}

.button.button-arrow-right.button-sm:before {
  right: 10px;
  margin-top: -4px;
  width: 8px;
  height: 8px;
}

.button.button-arrow-right.button-xs {
  padding: 3px 16px 3px 6px;
}

.button.button-arrow-right.button-xs:before {
  right: 6px;
  margin-top: -4px;
  width: 8px;
  height: 8px;
}

.button.button-icon {
  display: flex;
}

.button.button-icon span {
  margin-left: 10px;
}

.button.button-arrow-left {
  padding: 8px 15px 8px 25px;
  border-color: rgba(119, 119, 119, 0.6);
  color: #555555;
  background-color: white;
}

.button.button-arrow-left:before {
  content: "";
  position: absolute;
  top: 50%;
  right: auto;
  bottom: auto;
  left: 10px;
  width: 10px;
  height: 10px;
  margin-top: -5px;
  border-bottom: 2px solid #555555;
  border-right: 2px solid #555555;
  border-radius: 2px;
  transform: rotate(135deg);
}

.button.button-arrow-left:hover,
.button.button-arrow-left:focus {
  background-color: #e6e6e6;
}

.button.button-arrow-left:active {
  background-color: #cccccc;
}

.button.button-arrow-left.active {
  background-color: #cccccc;
}

.button.button-arrow-left.button-lg {
  padding: 16px 30px 16px 44px;
}

.button.button-arrow-left.button-lg:before {
  left: 18px;
  margin-top: -6px;
  width: 12px;
  height: 12px;
}

.button.button-arrow-left.button-sm {
  padding: 5px 10px 5px 20px;
}

.button.button-arrow-left.button-sm:before {
  left: 10px;
  margin-top: -4px;
  width: 8px;
  height: 8px;
}

.button.button-arrow-left.button-xs {
  padding: 3px 6px 3px 16px;
}

.button.button-arrow-left.button-xs:before {
  left: 6px;
  margin-top: -4px;
  width: 8px;
  height: 8px;
}

.addButton {
  position: relative;
  vertical-align: bottom;
  display: inline-block;
  min-width: 26px;
  width: 26px;
  height: 26px;
  margin-right: 10px;
  border-radius: 50%;
  border: 2px solid #5cb85c;
  text-align: center;
}

.addButton:after,
.addButton:before {
  content: "";
  position: absolute;
  top: 50%;
  right: auto;
  bottom: auto;
  left: 50%;
  width: 14px;
  height: 2px;
  display: block;
  background: #5cb85c;
  margin-left: -7px;
  margin-top: -1px;
}

.addButton:before {
  transform: rotate(90deg);
}

.deleteButton,
.dwButton,
.upButton {
  position: relative;
  min-width: 30px;
  width: 30px;
  height: 30px;
  vertical-align: bottom;
  display: inline-block;
  padding: 0;
  margin: 15px 10px 0 0;
  text-align: center;
  background-size: cover;
  cursor: pointer;
  transition: all 0.4s ease;
}

@media (min-width: 0) and (max-width: 499px) {
  .deleteButton,
  .dwButton,
  .upButton {
    margin: 0 10px 20px 0;
  }
}

.deleteButton:hover,
.deleteButton:focus,
.dwButton:hover,
.dwButton:focus,
.upButton:hover,
.upButton:focus {
  opacity: 0.5;
}

.deleteButton:active,
.dwButton:active,
.upButton:active {
  opacity: 0.7;
}

.deleteButton {
  background-image: url("./icon/btn_cancel.svg");
}

.dwButton {
  background-image: url("./icon/btn_dw.svg");
}

.upButton {
  background-image: url("./icon/btn_up.svg");
}

.btn-wrapper {
  display: flex;
  align-items: center;
}




