@import "../../sass/mixin";
@import "../../sass/vars";
@import "../../sass/media";

$steps-tip-before_width: 3px;
$steps-tip-before_height: 100%;
$steps-tip-before_top: 0;
$steps-tip-before_left: 20px;
$steps-border-radius: 4px;
$steps_padding-bottom: 40px;
$steps_padding-left: 60px;
.steps {
  margin: 0;
  padding: 0 0 $steps_padding-bottom $steps_padding-left;
  position: relative;
  &:before {
    @include pseudo($steps-tip-before_width, $steps-tip-before_height, $steps-tip-before_top, auto, auto, $steps-tip-before_left);
    border-radius: $steps-border-radius; }

  $steps__caption-font-size:  18px;

  $steps__caption-span_width: 30px;
  $steps__caption-span_height: $steps__caption-span_width;
  $steps__caption-span_top: -5px;
  $steps__caption-span_left: -53px;
  $steps__caption-span_font-size: 17px;
  $steps__caption-span_padding-left: 7px;
  $steps__caption-span_padding-top: 2px;
  $steps__caption-span_bg-color: white;
  $steps__caption-span_border-width: 3px;
  $steps__caption-span_border-radius: 50%;
  $steps__caption-span_outline-width: 5px;
  $steps__caption-span_outline-color: transparent;
  .steps__caption {
    list-style: none;
    position: relative;
    @include font($general-ff-helvetica, $general-font-weight-bold, $steps__caption-font-size);
    margin: 0 0 20px 0;
    & span {
      @include pseudo($steps__caption-span_width, $steps__caption-span_height, $steps__caption-span_top, auto, auto, $steps__caption-span_left);
      display: block;
      padding-left: $steps__caption-span_padding-left;
      padding-top: $steps__caption-span_padding-top;
      background: $steps__caption-span_bg-color;
      font-size: $steps__caption-span_font-size;
      font-weight: normal;
      border-radius: $steps__caption-span_border-radius;
      border: $steps__caption-span_border-width solid;
      outline: $steps__caption-span_outline-width solid $steps__caption-span_outline-color;
      z-index: 3; } }

  $steps__content-font-size: 18px;
  $steps__content-font-color: #535B63;
  $steps__content-line-height: 28px;
  .steps__content {
    list-style: none;
    @include font($general-ff-helvetica, $general-font-weight, $steps__content-font-size, $steps__content-font-color, $steps__content-line-height); } }

$steps-bg-gradient-to: rgba(255,255,255,1);

@each $name, $color in $color-steps-theme {
  .steps.#{$name} {
    &:before {
      background: linear-gradient(to bottom, $steps-bg-gradient-to 0%, #{$color} 100%); }
    & .steps__caption span {
      border-color: #{$color};
      color: #{$color}; } } }



