.download-files-wrapper {
  display: flex;
}

.download-files-wrapper-vertical {
  flex-direction: column;
}

.download-files__item {
  display: flex;
  width: 100%;
  margin-bottom: 20px;
  text-decoration: none;
}

.download-files__item:focus,
.download-files__item:active {
  text-decoration: none;
}

.download-files__item:hover .item__text .item__download {
  color: #00457b;
  text-decoration: underline;
}

.download-files__item .item__img {
  width: 2.8125rem;
  min-width: 2.8125rem;
  margin-right: 10px;
}

.download-files__item .item__img img {
  display: block;
  width: 100%;
  height: auto;
}

.download-files__item .item__text .item__name {
  display: block;
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1.25rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
  margin: 0;
  word-break: break-all;
}

.download-files__item .item__text .item__download {
  display: block;
  margin-top: 10px;
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1rem;
  color: #0063b0;
  line-height: 1.25rem;
  font-style: normal;
}

.download-files__item-lg .item__img {
  width: 3.75rem;
  min-width: 3.75rem;
  margin-right: 20px;
}

.download-files__item-lg .item__text .item__name {
  font-size: 24px;
  line-height: 2.5rem;
}

.download-files__item-lg .item__text .item__download {
  font-size: 20px;
}

.download-files__item-sm .item__img {
  width: 1.875rem;
  min-width: 1.875rem;
  margin-right: 10px;
}

.download-files__item-sm .item__text .item__name {
  font-size: 16px;
}

.download-files__item-sm .item__text .item__download {
  margin-top: 3px;
  font-size: 14px;
}




