.titleStep .titleStep__text {
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1.0625rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
}

.titleStep .titleStep__text:first-child {
  margin-top: 0;
}

.titleStep .titleStep__text-bold {
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: bold;
  font-size: 1.0625rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
  margin-right: 10px;
}

.titleStep__list {
  margin: 0;
  padding: 20px 0 20px 60px;
}

.titleStep__list .list__caption {
  position: relative;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: bold;
  font-size: 1.5rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
  list-style: none;
  padding-bottom: 20px;
}

.titleStep__list .list__caption:after {
  box-sizing: border-box;
  content: "";
  position: absolute;
  top: auto;
  right: auto;
  bottom: 10px;
  left: -46px;
  width: 15px;
  height: 15px;
  border-bottom: 3px solid;
  border-right: 3px solid;
  transform: rotate(45deg);
  background-color: transparent;
}

.titleStep__list .list__caption:before {
  content: "";
  position: absolute;
  top: -10px;
  right: auto;
  bottom: auto;
  left: -40px;
  width: 3px;
  height: 100%;
  border-radius: 4px;
}

.titleStep__list .list__content {
  list-style: none;
  margin-bottom: 20px;
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1.0625rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
}

.titleStep__list.transportAndDriving .list__caption:after {
  border-color: #2d7daf;
}

.titleStep__list.transportAndDriving .list__caption:before {
  background-color: #2d7daf;
}

.titleStep__list.primary .list__caption:after {
  border-color: #4d92c8;
}

.titleStep__list.primary .list__caption:before {
  background-color: #4d92c8;
}




