@mixin media-lg() {
  @media (min-width: $lg) {
    @content; } }

@mixin media-md() {
  @media (min-width: $md) and (max-width: $lg) {
    @content; } }

@mixin media-sm() {
  @media  (min-width: $sm) and (max-width: $md) {
    @content; } }

@mixin media-xs() {
  @media  (max-width: $sm) {
    @content; } }



