@import "./mixin";
@import './vars';
@import './media';

* {
  box-sizing: border-box; }

$form-margin-bottom: 40px;
.form {
  margin-bottom: $form-margin-bottom; }

.form-group {
  margin-bottom: 20px;
  @include small {
    margin-bottom: 5px; } }

.input-group {
  display: flex;
  @include small {
    flex-wrap: wrap; }
  & .input-wrapper {
    margin-left: 20px;
    margin-bottom: 20px;
    @include small {
      margin-left: 0; }
    &:first-child {
      margin-left: 0; } } }

$form__caption-number-font-size: 40px;
$form__caption-text-font-size: 18px;
.form__caption {
  display: flex;
  align-items: center;
  margin: 40px 0;
  & .form__caption-number {
    @include font($general-ff-ekibastuz, $general-font-weight, $form__caption-number-font-size);
    margin-right: 20px; }
  & .form__caption-text {
    @include font($general-ff-ekibastuz, $general-font-weight-bold, $form__caption-text-font-size); } }

$form-error-input-border-width: 1px;
$form-error-input-border-color: color('accent');
.input-wrapper.field-error {
  & input[type='text'], input[type='email'], textarea, select, input[type='date'] {
    border: $form-error-input-border-width solid $form-error-input-border-color; } }

$form-input-padding-top: 10px;
$form-input-padding-right: 35px;
$form-input-padding-bottom: $form-input-padding-top;
$form-input-padding-left: 20px;
$form-input-font-size: 17px;
$form-input-font-color: color('text', 'dark');
$form-input-line-height: 32px;
$form-input-border-color: color('text', 'lightGray','light');
$form-input-border-color-focus: color('shadowForm');
$form-input-border-width: 1px;
$form-input-box-shadow-color: rgba(0,0,0,0);
$form-input-box-shadow-color-focus: $form-input-border-color-focus;
$form-input-transition: border-color .25s,box-shadow .25s;

$form-input-label-top: 18px;
$form-input-label-left: 20px;
$form-input-label-font-size: 18px;
$form-input-label-font-color: color('text', 'lightGray');
$form-input-label-line-height: 1.2;
$form-input-label-transition: all .4s ease-in-out;

$form-input-label-font-size-valid: 10px;
$form-input-label-font-color: color('text', 'lightGray');
$form-input-label-line-height: 1.3;
.input-wrapper {
  & input[type='text'], input[type='email'], textarea, select, input[type='date'], input[type='search'], input[type='datetime-local'], .color-picker {
    position: relative;
    width: 100%;
    height: 60px;
    @include inline-block();
    padding: $form-input-padding-top $form-input-padding-right $form-input-padding-bottom $form-input-padding-left;
    margin: 0;
    @include font($general-ff-helvetica, $general-font-weight, $form-input-font-size, $form-input-font-color, $form-input-line-height );
    outline: none;
    border: $form-input-border-width solid $form-input-border-color;
    border-radius: 0;
    resize: none;
    background: color('white');
    box-shadow: 0 0 0 4px $form-input-box-shadow-color;
    transition: $form-input-transition;
    cursor: pointer;
    appearance: none;
    &:focus {
      box-shadow: 0 0 0 4px $form-input-box-shadow-color-focus;
      border-color: $form-input-border-color-focus;
      @include small {
        box-shadow: 0 0 0 2px $form-input-box-shadow-color-focus; } }
    & + label, + div + label {
      position: absolute;
      top: $form-input-label-top;
      left: $form-input-label-left;
      @include inline-block();
      @include font($general-ff-helvetica, $general-font-weight, $form-input-label-font-size, $form-input-label-font-color, $form-input-label-line-height );
      cursor: pointer;
      margin-bottom: 4px;
      white-space: normal!important;
      z-index: 1;
      transition: $general-transition;
      @include small {
        top: -20px;
        left: 0; } } }
  & textarea {
    $form-text-area-height: 200px;
    height: $form-text-area-height; }
  & input[type='text']:focus + label, input[type='text'].label-min + label, input[type='date']:focus + label, input[type='date'].label-min + label, input[type='email']:focus + label, input[type='email'].label-min + label, textarea:focus + label, .label-min textarea + label,textarea.label-min + label, input[type='text']:focus + div + label, input[type='text'].label-min + div + label, select:focus + label, .label-min select + label, input[type='datetime-local'] + label, .color-picker.label-min  label {
    @include font($general-ff-helvetica, $general-font-weight, $form-input-label-font-size-valid, $form-input-label-font-color, $form-input-label-line-height );
    top: 3px;
    z-index: 1;
    @include small {
      top: -20px;
      left: 0;
      font-size: 14px; } } }

$form-disabled-input-bg-color: #e4e7e9;
$form-disabled-input-border-color: #db4056;
$form-disabled-input-label-font-size: 10px;
$form-disabled-input-label-color: color('text', 'light', 'darken');
$form-disabled-input-label-line-height: 1.3;
input[type='text'], input[type='email'], textarea, select, input[type='date'] {
  &:disabled {
    background: $form-disabled-input-bg-color;
    cursor: no-drop;
    & + label, + div + label {
      @include font($general-ff-ekibastuz, $general-font-weight-bold, $form-disabled-input-label-font-size, $form-disabled-input-label-color, $form-disabled-input-label-line-height );
      top: 3px;
      z-index: 5;
      @include small {
        top: -18px;
        left: 0;
        font-size: rem-calcF(14px); } } } }

$form-input-under-text-font-size: 12px;
$form-input-under-text-font-color: #4c4c4c;
$form-input-under-text-line-height: 1.3;
.input__under-text {
  position: relative;
  display: block;
  @include font($general-ff-helvetica, $general-font-weight, $form-input-under-text-font-size, $form-input-under-text-font-color, $form-input-under-text-line-height );
  text-align: right; }

//        Checkbox         //

$checkbox-label-before-box-shadow-color-hover: #bfe5ff;

$checkbox-label-before-width: 20px;
$checkbox-label-before-height: $checkbox-label-before-width;
$checkbox-label-before-border-color: #CDD1D4;
$checkbox-label-before-transition: box-shadow .5s ease;

$checkbox-label-after-width: 8px;
$checkbox-label-after-height: 10px;
$checkbox-label-after-bottom: 50%;
$checkbox-label-after-margin-bottom: -$checkbox-label-after-width/2;
$checkbox-label-after-left: 6px;
$checkbox-label-after-bg-color: #6CA9D2;
$checkbox-label-after-border-color: white;
$checkbox-label-after-border-width: 3px;

$checkbox-disabled-checked_before-border-color: #859097;
$checkbox-disabled-checked_before-border-width: 3px;
$checkbox-disabled-checked_before-bg-color: #d3dbe0;
$checkbox-disabled-checked_before-bg-color-hover: #e7e8e8;
$checkbox-disabled-checked_after-bg-color: #d3dbe0;

.checkboxes_inline .checkboxes {
  flex-direction: row;
  flex-wrap: wrap; }

.checkboxes {
  margin-top: 50px;
  & .checkbox {
    margin-bottom: 20px;
    margin-top: 0; }
  & + label {
    position: absolute;
    top: -35px; }
  & label {
    padding-left: 0; } }

.checkboxes {
  margin-left: 0;
  display: flex;
  align-items: baseline;
  flex-direction: column;
  & .checkbox-inline + & .checkbox-inline {
    margin-left: 0; }
  & .checkbox, & .checkbox-inline {
    margin-left: 0;
    display: flex;
    align-items: baseline;
    flex-direction: column;
    margin-bottom: 20px; }
  & + label {
    position: absolute;
    top: -35px; }
  & label {
    padding-left: 0; }
  & label span {
    cursor: pointer;
    display: flex;
    align-items: center;
    position: relative;
    margin-right: 20px;
    padding-left: 0;
    margin-left: 0;
    & input[type="checkbox"] {
      display: none;
      & + span {
        cursor: pointer;
        display: flex;
        align-items: center;
        position: relative;
        top: 0;
        left: 0;
        margin-right: 20px;
        &:hover:before {
          box-shadow: 0 0 0 4px $checkbox-label-before-box-shadow-color-hover; }
        &::before {
          content: '';
          @include inline-block(middle);
          transition: $checkbox-label-before-transition;
          width: $checkbox-label-before-width;
          height: $checkbox-label-before-height;
          min-width: $checkbox-label-before-width;
          min-height: $checkbox-label-before-height;
          margin-right: 5px;
          border-radius: 4px;
          border: 1px solid $checkbox-label-before-border-color;
          cursor: pointer;
          z-index: 10;
          background: color('white'); } }
      &:checked + span {
        &::before {
          $checkbox-checked-label-before-bg-color: #6CA9D2;
          background-color: $checkbox-checked-label-before-bg-color; }
        &::after {
          @include pseudo($checkbox-label-after-width, $checkbox-label-after-height, auto, auto, $checkbox-label-after-bottom, $checkbox-label-after-left);
          background-color: $checkbox-label-after-bg-color;
          margin-bottom: $checkbox-label-after-margin-bottom;
          border-radius: 1px;
          border: {
            left: none;
            top: none;
            right: $checkbox-label-after-border-width solid $checkbox-label-after-border-color;
            bottom: $checkbox-label-after-border-width solid $checkbox-label-after-border-color; }
          transform: rotate(45deg);
          cursor: pointer;
          z-index: 20; } } }
    & input[disabled] {
      &:checked + span {
        &::before {
          background-color: $checkbox-disabled-checked_before-bg-color; }
        &::after {
          background-color: $checkbox-disabled-checked_after-bg-color;
          border: {
            right: $checkbox-disabled-checked_before-border-width solid $checkbox-disabled-checked_before-border-color;
            bottom: $checkbox-disabled-checked_before-border-width solid $checkbox-disabled-checked_before-border-color; } } }
      & + span {
        &:hover:before {
          box-shadow: none; }
        &::before {
          background-color: $checkbox-disabled-checked_before-bg-color-hover; } } } } }

$checkbox-error: color('danger');
.input__under-text.checkbox-error {
  position: absolute;
  left: 0;
  color: $checkbox-error;
  bottom: -15px;
  @include small {
    bottom: -10px; } }


//         radio        //


$radio-label-box-shadow-color-hover: #bfe5ff;

$radio-label-before-width: 20px;
