          border-width: $radio-checked-before-border-width; }
        &::after {
          @include pseudo($radio-label-after-width, $radio-label-after-height, $radio-label-after-top, auto, auto, $radio-label-after-left);
          margin-top: -3px;
          border-radius: 50%;
          background-color: $radio-label-after-bg-color;
          cursor: pointer;
          z-index: 20; } } } }
  & label {
    padding-left: 0;
    & span {
      cursor: pointer;
      display: flex;
      align-items: center;
      position: relative;
      min-width: 50px;
      padding-left: 0;
      margin-left: 0;
      & input[type="radio"] {
        display: none;
        & + span {
          cursor: pointer;
          display: flex;
          align-items: center;
          position: relative;
          top: 0;
          left: 0;
          margin-right: 20px;
          &:hover:before {
