            background: white;
            z-index: 10; } }
        &:checked + span {
          &::before {
            border-color: $radio-checked-before-border-color; }
          &::after {
            @include pseudo($radio-label-after-width, $radio-label-after-height, $radio-label-after-top, auto, auto, $radio-label-after-left);
            margin-top: -3px;
            border-radius: 50%;
            background-color: $radio-label-after-bg-color;
            cursor: pointer;
            z-index: 20; } } } }
    & input[disabled] {
      &:checked + label {
        &::before {
          border: $radio-disabled-checked_before-border-width solid $radio-disabled-checked_before-border-color;
          background-color: $radio-disabled-checked_before-bg-color; }
        &::after {
          background-color: $radio-disabled-checked_after-bg-color; } }
      & + label {
        &:hover:before {
          box-shadow: none; }
        &::before {
          background-color: $radio-disabled-checked_before-bg-color-hover; } } } } }

$radio-error-color: color('danger');
.input__under-text.radio-error {
  position: absolute;
  left: 0;
  color: $radio-error-color;
  @include small {
    bottom: -10px; } }



