@import "../../sass/mixin";
@import "../../sass/vars";
@import "../../sass/media";
@import "../../sass/fonts";

$base-caption-vertical-step: 40px;
$base-caption-ff: $general-ff-ekibastuz;
$base-caption-font-weight: $general-font-weight-light;
.base-caption {
  margin-bottom: $base-caption-vertical-step;
  &.base-caption--center {
    text-align: center; }
  &.base-caption--left {
    text-align: left; }
  &.base-caption--right {
    text-align: right; } }

h1.base-caption {
  @include font($base-caption-ff, $general-font-weight-light, 42px, $general-font-color, 48px);
  @include medium {
    font-size: 30px;
    line-height: 34px; }
  @include small {
    font-size: 22px;
    line-height: 25px; } }

h2.base-caption {
  @include font($base-caption-ff, $general-font-weight, 20px, color('text'), 24px);
  @include medium {
    font-size: 18px;
    line-height: 20px; }
  @include small {
    font-size: 16px;
    line-height: 20px; } }

h3.base-caption {
  @include font($base-caption-ff, $general-font-weight-bold, 18px, $general-font-color, 20px);
  @include medium {
    font-size: 17px; }
  @include small {
    font-size: 15px; } }

h4.base-caption {
  @include font($base-caption-ff, $general-font-weight-bold, 15px, $general-font-color, 20px); }




