@import "../../../../sass/mixin";
@import "../../../../sass/vars";
@import "../../../../sass/media";

$article-padding: 20px;
$article-ff: $general-ff-ekibastuz;
$article-font-weight: $general-font-weight;
$article-font-weight-link: $general-font-weight-bold;
$article-font-weight_h1-6: $general-font-weight-light;
$article-line-height_h1-3: $general-font-line-height-large;
$article-line-height_h4-6: $general-font-line-height;
$article-font-size: 17px;
$article-color: $general-font-color;
$article-link-color: color('primary','light');
$article-link-color-hover: color('primary');
.article {
  background-color: white;
  & pre {
    & code {
      background: transparent;
      padding: 0; } }

  & code, & pre {
    background: rgba(#c5fffc, 0.3);
    padding: 10px;
    font-weight: 900; }

  & table {
    border-collapse: collapse;
    @include font($article-ff, $article-font-weight, $article-font-size); }

  & td, & th {
    padding: 5px 10px;
    border: 1px solid #ccc; }

  & hr {
    margin: 20px 0; }
  & p {
    @include font($article-ff, $article-font-weight, $article-font-size);
    margin-bottom: $article-padding; }
  & a {
    @include font($article-ff, $article-font-weight-link, $article-font-size, $article-link-color);
    &:hover {
      color: $article-link-color-hover; } }

  & h1, h2, h3, h4, h5, h6 {
    margin-top: 0;
    margin-bottom: $article-padding; }

  & h1 {
    @include font($article-ff, $article-font-weight_h1-6, 40px, $article-color, $article-line-height_h1-3); }
  & h2 {
    @include font($article-ff, $article-font-weight_h1-6, 32px, $article-color, $article-line-height_h1-3); }
  & h3 {
    @include font($article-ff, $article-font-weight_h1-6, 24px, $article-color, $article-line-height_h1-3); }
  & h4 {
    @include font($article-ff, $article-font-weight_h1-6, 20px, $article-color, $article-line-height_h4-6); }
  & h5 {
    @include font($article-ff, $article-font-weight_h1-6, 16px, $article-color, $article-line-height_h4-6); }
  & h6 {
    @include font($article-ff, $article-font-weight_h1-6, 14px, $article-color, $article-line-height_h4-6); }

  & ul {
    padding-left: 20px;
    margin-bottom: $article-padding; }
  & ol {
    padding-left: $article-padding;
    margin-bottom: $article-padding; }
  & li {
    @include font($article-ff, $article-font-weight, $article-font-size);
    margin-bottom: $article-padding/2; }

  & img {
    display: block;
    max-width: 800px;
    width: 100%;
    height: 100%;
    margin: 0 auto $article-padding; } }



