@import "../../sass/mixin";
@import "../../sass/vars";
@import "../../sass/media";

.usluga-card-wrapper .usluga-card:hover .usluga-card__links .usluga-card__link {
    text-decoration: underline; }

.usluga-card {
  $usluga-card__vertical-step-normal: $general-vertical-step;
  $usluga-card__transition: $general-transition;
  $usluga-card__width: 380px;
  $usluga-card__border-radius: 5px;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: $usluga-card__width;
  padding: $usluga-card__vertical-step-normal;
  margin-bottom: $usluga-card__vertical-step-normal;
  border: 1px solid transparent;
  border-radius: $usluga-card__border-radius;
  text-decoration: none;
  transition: $usluga-card__transition;
  @include small {
    align-items: center; }
  &:hover {
    $popular-services-bg-color: color('white', 'dark','lighter');
    $popular-services-border-color: color('text','lighter');

    background-color: $popular-services-bg-color;
    border-color: $popular-services-border-color;
    & .usluga-card__links .usluga-card__link {
      color: color('primary'); } } }

.usluga-card__links {
  margin-top: auto; }

.usluga-card__link {
  $usluga-card__link-ff: $general-ff-ekibastuz;
  $usluga-card__link-font-weight: $general-font-weight;
  $usluga-card__link-font-size: $general-font-size;
  $usluga-card__link-font-color: color('text');
  $usluga-card__link-line-height: $general-font-line-height;
  $usluga-card__link-padding-top: 10px;
  $usluga-card__link-padding-left: 92px;

  display: block;
  @include font($usluga-card__link-ff, $usluga-card__link-font-weight, $usluga-card__link-font-size, $usluga-card__link-font-color, $usluga-card__link-line-height);
  margin: 0;
  padding: $usluga-card__link-padding-top 0 0 $usluga-card__link-padding-left;
  text-decoration: none;
  @include small {
    text-align: center;
    padding-left: 0; }
  &:hover {
    text-decoration: underline; } }



