.usluga-card-wrapper .usluga-card:hover .usluga-card__links .usluga-card__link {
  text-decoration: underline;
}

.usluga-card {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 380px;
  padding: 20px;
  margin-bottom: 20px;
  border: 1px solid transparent;
  border-radius: 5px;
  text-decoration: none;
  transition: all 0.4s ease;
}

@media (min-width: 0) and (max-width: 499px) {
  .usluga-card {
    align-items: center;
  }
}

.usluga-card:hover {
  background-color: rgba(230, 230, 230, 0.2);
  border-color: #eeeeee;
}

.usluga-card:hover .usluga-card__links .usluga-card__link {
  color: #0063b0;
}

.usluga-card__links {
  margin-top: auto;
}

.usluga-card__link {
  display: block;
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1rem;
  color: #555555;
  line-height: 1.25rem;
  font-style: normal;
  margin: 0;
  padding: 10px 0 0 92px;
  text-decoration: none;
}

@media (min-width: 0) and (max-width: 499px) {
  .usluga-card__link {
    text-align: center;
    padding-left: 0;
  }
}

.usluga-card__link:hover {
  text-decoration: underline;
}




