.panel-wrapper {
  width: 100%;
  margin-bottom: 10px;
  background-color: #fff;
  border: 1px solid transparent;
  border-radius: 4px;
  transition: all 0.4s ease;
}

.panel-wrapper .panel__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  border-bottom: 1px solid transparent;
  border-top-right-radius: 3px;
  border-top-left-radius: 3px;
  padding: 10px 15px;
  transition: background 0.4s ease;
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
}

@media (min-width: 0) and (max-width: 499px) {
  .panel-wrapper .panel__header {
    padding: 5px 10px;
  }
}

.panel-wrapper .panel__header--none-border-bottom {
  border-color: transparent !important;
}

.panel-wrapper .panel__header--cursor-pointer {
  cursor: pointer;
}

.panel-wrapper .panel__header .base-query_state-name {
  margin-left: auto;
}

.panel-wrapper .panel__header .panel__header-text {
  margin-right: auto;
}

@media (min-width: 0) and (max-width: 499px) {
  .panel-wrapper .panel__header .panel__header-text {
    margin-bottom: 10px;
  }
}

.panel-wrapper .panel__header .panel__header-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.panel-wrapper .panel__header .panel__header-info .arrow {
  margin-left: auto;
}

.panel-wrapper .panel__header .panel__header-info .label {
  margin-right: 20px;
}

.panel-wrapper .panel__body {
  padding: 15px;
  transition: all 0.4s ease;
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
}

@media (min-width: 0) and (max-width: 499px) {
  .panel-wrapper .panel__body {
    padding: 5px;
  }
}

.panel-wrapper .panel__body--none {
  display: none;
}

.panel-wrapper.panel-color--default {
  border-color: #e6e6e6;
}

.panel-wrapper.panel-color--default .panel__header {
  background-color: #f2f2f2;
  border-color: #e6e6e6;
}

.panel-wrapper.panel-color--default .panel__header:hover {
  background-color: #e6e6e6;
}

.panel-wrapper.panel-color--warning {
  border-color: rgba(237, 155, 37, 0.4);
}

.panel-wrapper.panel-color--warning .panel__header {
  color: #c77c11;
  background-color: rgba(245, 198, 131, 0.4);
  border-color: rgba(237, 155, 37, 0.4);
}

.panel-wrapper.panel-color--warning .panel__header:hover {
  background-color: rgba(241, 176, 84, 0.4);
}

.panel-wrapper.panel-color--danger {
  border-color: rgba(206, 18, 61, 0.4);
}

.panel-wrapper.panel-color--danger .panel__header {
  color: #890825;
  background-color: rgba(240, 86, 121, 0.4);
  border-color: rgba(206, 18, 61, 0.4);
}

.panel-wrapper.panel-color--danger .panel__header:hover {
  background-color: rgba(236, 39, 84, 0.4);
}

.panel-wrapper.panel-color--success {
  border-color: rgba(74, 170, 74, 0.4);
}

.panel-wrapper.panel-color--success .panel__header {
  color: #357935;
  background-color: rgba(141, 205, 141, 0.4);
  border-color: rgba(74, 170, 74, 0.4);
}

.panel-wrapper.panel-color--success .panel__header:hover {
  background-color: rgba(106, 189, 106, 0.4);
}

.panel-wrapper.panel-color--info {
  border-color: rgba(85, 184, 253, 0.4);
}

.panel-wrapper.panel-color--info .panel__header {
  color: #39acfb;
  background-color: rgba(186, 226, 254, 0.4);
  border-color: rgba(85, 184, 253, 0.4);
}

.panel-wrapper.panel-color--info .panel__header:hover {
  background-color: rgba(136, 205, 253, 0.4);
}

.panel-wrapper.panel-color--primary {
  border-color: #0063b0;
}

.panel-wrapper.panel-color--primary .panel__header {
  color: white;
  background-color: #0063b0;
  border-color: #0063b0;
}

.panel-wrapper.panel-color--primary .panel__header:hover {
  background-color: #4d92c8;
}




