@font-face {
  font-family: "Helvetica Neue";
  src: url("../../fonts/HelveticaNeue.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Helvetica Neue";
  src: url("../../fonts/HelveticaNeueBold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: "Ekibastuz";
  src: url("../../fonts/Ekibastuz-Regular.otf");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Ekibastuz";
  src: url("../../fonts/Ekibastuz-Bold.otf");
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: "Ekibastuz";
  src: url("../../fonts/Ekibastuz-Light.otf");
  font-weight: 300;
  font-style: normal;
}

.base-caption {
  margin-bottom: 40px;
}

.base-caption.base-caption--center {
  text-align: center;
}

.base-caption.base-caption--left {
  text-align: left;
}

.base-caption.base-caption--right {
  text-align: right;
}

h1.base-caption {
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 300;
  font-size: 2.625rem;
  color: #000;
  line-height: 48px;
  font-style: normal;
}

@media (max-width: 768px) {
  h1.base-caption {
    font-size: 30px;
    line-height: 34px;
  }
}

@media (min-width: 0) and (max-width: 499px) {
  h1.base-caption {
    font-size: 22px;
    line-height: 25px;
  }
}

h2.base-caption {
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1.25rem;
  color: #555555;
  line-height: 24px;
  font-style: normal;
}

@media (max-width: 768px) {
  h2.base-caption {
    font-size: 18px;
    line-height: 20px;
  }
}

@media (min-width: 0) and (max-width: 499px) {
  h2.base-caption {
    font-size: 16px;
    line-height: 20px;
  }
}

h3.base-caption {
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: bold;
  font-size: 1.125rem;
  color: #000;
  line-height: 20px;
  font-style: normal;
}

@media (max-width: 768px) {
  h3.base-caption {
    font-size: 17px;
  }
}

@media (min-width: 0) and (max-width: 499px) {
  h3.base-caption {
    font-size: 15px;
  }
}

h4.base-caption {
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: bold;
  font-size: 0.9375rem;
  color: #000;
  line-height: 20px;
  font-style: normal;
}

.card-caption {
  display: flex;
  align-items: center;
}

@media (min-width: 0) and (max-width: 499px) {
  .card-caption {
    flex-direction: column;
    align-items: center;
  }
}

.card-caption .card-caption__img-wrapper {
  width: 72px;
  min-width: 72px;
  height: 80px;
  margin: 0 20px 0 0;
}

@media (min-width: 0) and (max-width: 767px) {
  .card-caption .card-caption__img-wrapper {
    margin: 0 0 10px 0;
  }
}

.card-caption .card-caption__img-wrapper img {
  display: block;
  width: 100%;
  height: auto;
}

.card-caption .card-caption__name {
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: bold;
  font-size: 1.25rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
  margin: 0;
}

@media (min-width: 0) and (max-width: 767px) {
  .card-caption .card-caption__name {
    text-align: center;
  }
}

.card-caption.large,
.card-caption-lg {
  margin-bottom: 20px;
}

.card-caption.large .card-caption__name,
.card-caption-lg .card-caption__name {
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 300;
  font-size: 2.625rem;
  color: #000;
  line-height: 48px;
  font-style: normal;
}

.card-caption-sm .card-caption__img-wrapper {
  width: 47px;
  min-width: 47px;
  height: 52px;
  margin-right: 10px;
}

.card-caption-sm .card-caption__name {
  font-size: 16px;
}

.card-caption-xs .card-caption__img-wrapper {
  width: 29px;
  min-width: 29px;
  height: 32px;
  margin-right: 5px;
}

.card-caption-xs .card-caption__name {
  font-size: 15px;
}




