@import "../../sass/mixin";
@import "../../sass/vars";
@import "../../sass/media";

.alert {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  margin-bottom: 20px;
  border: 1px solid transparent;
  border-radius: 4px;
  &-default {
    background-color: color('text','light','dark');
    border-color: color('text');
    color: color('text','dark'); }
  &-primary {
    background-color: color('primary','light','dark');
    border-color: color('primary');
    color: color('primary','dark'); }
  &-success {
    background-color: color('success','light','dark');
    border-color: color('success');
    color: color('success','dark'); }
  &-danger {
    background-color: color('danger','light','dark');
    border-color: color('danger');
    color: color('danger','dark'); }
  &-warning {
    background-color: color('warning','light','dark');
    border-color: color('warning');
    color: color('warning','dark'); }
  &-info {
    background-color: color('info','light','dark');
    border-color: color('info');
    color: color('info','dark'); }
  &-lg {
    width: 100%; }
  &-md {
    width: 1000px; }
  &-sm {
    width: 600px; }
  &-xs {
    width: 320px; }

  &-dismissable {
    padding-right: 30px; }

  & .close {
    position: absolute;
    top: 10px;
    right: 10px;
    color: inherit; } }



