.container .container {
  width: 100%;
}

.container {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  margin-left: auto;
  margin-right: auto;
}

@media (min-width: 1220.2px) {
  .container {
    width: 1200.2px;
  }
}

@media (min-width: 992px) and (max-width: 1220.2px) {
  .container {
    width: 100%;
  }
}

.container-fluid {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
}




