.complaint-wrapper {
  margin-top: 20px;
}

.complaint-wrapper .complaint {
  margin-right: 20px;
  max-width: 800px;
}

.complaint-wrapper .complaint .complaint__caption {
  margin-top: 0;
  margin-bottom: 20px;
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: bold;
  font-size: 1.25rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
}

.complaint-wrapper .complaint .complaint__text {
  margin-bottom: 20px;
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
}

.complaint-wrapper a {
  text-decoration: none;
}

.complaint-wrapper a button {
  display: flex;
  justify-content: center;
  margin: 40px 0 0 0;
}

@media (min-width: 0) and (max-width: 499px) {
  .complaint-wrapper a button {
    margin: 20px 0 0 0;
  }
}

.complaint-wrapper a button svg {
  margin-left: 10px;
}




