.alert {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  margin-bottom: 20px;
  border: 1px solid transparent;
  border-radius: 4px;
}

.alert-default {
  background-color: rgba(119, 119, 119, 0.6);
  border-color: #555555;
  color: #333333;
}

.alert-primary {
  background-color: rgba(77, 146, 200, 0.6);
  border-color: #0063b0;
  color: #00457b;
}

.alert-success {
  background-color: rgba(141, 205, 141, 0.6);
  border-color: #5cb85c;
  color: #408140;
}

.alert-danger {
  background-color: rgba(240, 86, 121, 0.6);
  border-color: #e90e3f;
  color: #a30a2c;
}

.alert-warning {
  background-color: rgba(245, 198, 131, 0.6);
  border-color: #f0ad4e;
  color: #a87937;
}

.alert-info {
  background-color: rgba(186, 226, 254, 0.6);
  border-color: #9dd6fd;
  color: #6e96b1;
}

.alert-lg {
  width: 100%;
}

.alert-md {
  width: 1000px;
}

.alert-sm {
  width: 600px;
}

.alert-xs {
  width: 320px;
}

.alert-dismissable {
  padding-right: 30px;
}

.alert .close {
  position: absolute;
  top: 10px;
  right: 10px;
  color: inherit;
}




