.arrow {
  position: relative;
  vertical-align: middle;
  display: inline-block;
  padding: 0;
  border-right: solid;
  border-bottom: solid;
  box-sizing: border-box;
}

.arrow__right {
  transform: rotate(-45deg);
  margin-left: 5px;
}

.arrow__left {
  transform: rotate(135deg);
  margin-right: 10px;
  margin-bottom: 2px;
}

.arrow__top {
  transform: rotate(225deg);
  margin-left: 10px;
  margin-bottom: -2px;
}

.arrow__bottom {
  transform: rotate(45deg);
  margin-left: 10px;
  margin-bottom: 6px;
}

.arrow__lg {
  min-width: 14px;
  width: 14px;
  height: 14px;
  border-width: 2px;
}

.arrow__md {
  min-width: 11px;
  width: 11px;
  height: 11px;
  border-width: 2px;
}

.arrow__sm {
  min-width: 8px;
  width: 8px;
  height: 8px;
  border-width: 2px;
}

.arrow__xs {
  min-width: 5px;
  width: 5px;
  height: 5px;
  border-width: 1px;
}

.arrow__default {
  border-color: #000;
}

.arrow__success {
  border-color: #5cb85c;
}

.arrow__danger {
  border-color: #e90e3f;
}

.arrow__info {
  border-color: #9dd6fd;
}

.arrow__warning {
  border-color: #f0ad4e;
}

.arrow__primary {
  border-color: #0063b0;
}




