@import "../../sass/mixin";
@import "../../sass/vars";
@import "../../sass/media";

.nav {
  display: inline-block;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 20px;
  list-style: none; }

$table-filter_ff: $general-ff-ekibastuz;
$table-filter_fw: $general-font-weight-bold;
$table-filter_fsz: 14px;
$table-filter_border-radius: 4px;
$table-filter_color: color('text');
$table-filter_color-hover: color('text', 'black');
$table-filter_background-color-hover: color('primary-light', 'light','light');
$table-filter_padding-horizontal: 18px;
$table-filter_padding-vertical: $table-filter_padding-horizontal/2;
$table-filter_transition: background-color 0.2s ease;
$table-filter_active-border-color: color('primary-light', 'light');
$table-filter_active-background-color: color('primary-light');
$table-filter_active-color: color('text', 'white');
.nav > li {
  position: relative;
  display: block;
  float: left;
  border: 1px solid #97c3e0;
  border-radius: 0;
  &.disabled {
    background: color('text','light','light');
    cursor: no-drop; }
  &:first-child {
    border-bottom-left-radius: 4px;
    border-top-left-radius: 4px; }
  &:last-child {
    border-bottom-right-radius: 4px;
    border-top-right-radius: 4px; }
  & + li {
    margin-left: -1px; }
  & a {
    @include font($table-filter_ff, $table-filter_fw, $table-filter_fsz, $table-filter_color);
    padding: $table-filter_padding-vertical $table-filter_padding-horizontal;
    border-radius: 0;
    border: none;
    position: relative;
    display: block;
    text-decoration: none;
    &:hover, &:focus {
      background-color: $table-filter_background-color-hover;
      color: $table-filter_color-hover; } }
  &.active.disabled a {
    background: rgba(107, 169, 210, 0.58); }
  &.active a {
    background-color: $table-filter_active-background-color;
    color: $table-filter_active-color;
    cursor: default;
    &:hover, &:focus {
      background-color: $table-filter_active-background-color;
      color: $table-filter_active-color; } } }



