.input-wrapper input[type='text'],
.input-wrapper input[type='email'],
.input-wrapper textarea,
.input-wrapper select,
.input-wrapper input[type='date'],
.input-wrapper input[type='search'],
.input-wrapper input[type='datetime-local'],
.input-wrapper .color-picker {
  position: relative;
  width: 100%;
  height: 60px;
  vertical-align: top;
  display: inline-block;
  padding: 10px 35px 10px 20px;
  margin: 0;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1.0625rem;
  color: #333333;
  line-height: 32px;
  font-style: normal;
  outline: none;
  border: 1px solid rgba(153, 153, 153, 0.4);
  border-radius: 0;
  resize: none;
  background: white;
  box-shadow: 0 0 0 4px transparent;
  transition: border-color 0.25s, box-shadow 0.25s;
  cursor: pointer;
  appearance: none;
}

.input-wrapper input[type='text']:focus,
.input-wrapper input[type='email']:focus,
.input-wrapper textarea:focus,
.input-wrapper select:focus,
.input-wrapper input[type='date']:focus,
.input-wrapper input[type='search']:focus,
.input-wrapper input[type='datetime-local']:focus,
.input-wrapper .color-picker:focus {
  box-shadow: 0 0 0 4px #bfe5ff;
  border-color: #bfe5ff;
}

@media (min-width: 0) and (max-width: 499px) {
  .input-wrapper input[type='text']:focus,
  .input-wrapper input[type='email']:focus,
  .input-wrapper textarea:focus,
  .input-wrapper select:focus,
  .input-wrapper input[type='date']:focus,
  .input-wrapper input[type='search']:focus,
  .input-wrapper input[type='datetime-local']:focus,
  .input-wrapper .color-picker:focus {
    box-shadow: 0 0 0 2px #bfe5ff;
  }
}

.search-wrapper.input-wrapper {
  display: flex;
  padding: 10px;
  background: #9dd6fd;
  margin-bottom: 40px;
}

.search-wrapper.input-wrapper input[type='search'] {
  margin-right: 10px;
}

.send-button {
  position: relative;
  width: 75px;
  min-width: 75px;
  height: 60px;
  min-height: 60px;
  display: inline-block;
  line-height: 42px;
  border-radius: 4px;
  cursor: pointer;
  background-color: #0063b0;
  white-space: nowrap;
  outline: none;
  border: none;
  transition: all 0.4s ease;
}

.send-button:hover {
  background-color: #00457b;
}

.send-button .send-button__wrapper-img {
  position: absolute;
  margin-left: -12px;
  left: 50%;
  margin-top: -12px;
  top: 50%;
  width: 24px;
  height: 24px;
}

.send-button .send-button__wrapper-img img {
  display: block;
  width: 100%;
  height: auto;
}




