@import "../../sass/mixin";
@import "../../sass/vars";
@import "../../sass/media";

.news-wrapper {
  margin-top: 40px;
  width: 100%; }

$news_border-color: color('text','light','light');
$news_bg-color: color('secondary', 'base', 'lighter');
$news-caption_color-hover: color('black');
.news {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid $news_border-color;
  padding-top: 20px;
  transition: $general-transition;
  text-decoration: none;
  &:last-child {
    border: none; }
  &:hover {
    background-color: $news_bg-color;
    & .news__caption {
      color: $news-caption_color-hover; } }

  $news__date_ff: $general-ff-ekibastuz;
  $news__date_fw: $general-font-weight;
  $news__date_fsz: 14px;
  $news__date_color: color('text', 'lightGray');
  & .news__date {
    margin-bottom: 10px;
    @include font($news__date_ff, $general-font-weight, $news__date_fsz, $news__date_color);
    & .date__clock-wrapper {
      @include inline-block();
      width: 16px;
      min-width: 16px;
      margin-right: 10px;
      & img {
        @include img(); } } }

  $news__caption_ff: $general-ff-ekibastuz;
  $news__caption_fw: $general-font-weight-bold;
  $news__caption_fsz: 20px;
  $news__caption_color: color('primary');
  & .news__caption {
    margin-top: 0;
    margin-bottom: 10px;
    @include font($news__caption_ff, $news__caption_fw, $news__caption_fsz, $news__caption_color);
    transition: $general-transition;
    &.news__caption_padding {
      margin-bottom: 20px; } }
  & .news__text {
    margin-bottom: 20px;
    margin-top: 0;
    @include font($news__caption_ff); } }



