@mixin hidden-lg() {
  @include media-lg() {
    display: none; } }

@mixin hidden-md() {
  @include media-md() {
    display: none; } }

@mixin hidden-sm() {
  @include media-sm() {
    display: none; } }

@mixin hidden-xs() {
  @include media-xs() {
    display: none; } }



