@import 'vars';

@function color( $name: 'primary', $variant: $general-color-key, $opacity: 1 ) {
  $color: null;

  // Get the color spectrum
  $color-spectrum: map-get($general-pallet, $name);

  // Get the color variant
  @if $color-spectrum {
    $color: map-get($color-spectrum, $variant); }

  // Get the alpha setting
  $alpha: if(type-of($opacity) == 'number', $opacity, map-get($general-opacity, $opacity));

  // Set the alpha of the color
  @if $alpha {
    $color: rgba($color, $alpha); }

  @return $color; }

@function rem-calcF($size) {
  $remSize: ($size / $general-font-size) * 1rem;
  @return $remSize; }

@mixin rem-calc($size) {
  font-size: rem-calcF($size); }

@mixin font($font-family: $general-ff-helvetica, $font-weight: $general-font-weight, $font-size: $general-font-size, $font-color: $general-font-color, $font-line-height: $general-font-line-height, $font-style: normal) {
  font-family: $font-family;
  font-weight: $font-weight;
  font-size: rem-calcF($font-size);
  color: $font-color;
  line-height: $font-line-height;
  font-style: $font-style; }

@mixin img($img-display: block, $img-width: 100%, $img-height: auto) {
  display: $img-display;
  width: $img-width;
  height: $img-height; }

@mixin inline-block($inline-block-vertical-align: top) {
  vertical-align: $inline-block-vertical-align;
  display: inline-block; }

@mixin border($border-position, $border-color: color('text'), $border-radius: 0, $border-width: 1px, $border-style: solid) {
  @if $border-position == border {
    border: $border-width $border-style $border-color;
    border-radius: $border-radius; }
  @if $border-position == border-top {
    border-top: $border-width $border-style $border-color; }
  @if $border-position == border-right {
    border-right: $border-width $border-style $border-color; }
  @if $border-position == border-bottom {
    border-bottom: $border-width $border-style $border-color; }
  @if $border-position == border-left {
    border-left: $border-width $border-style $border-color; } }

@mixin pseudo($pseudo-width: 0, $pseudo-height: 0, $pseudo-top: auto, $pseudo-right: auto, $pseudo-bottom: auto, $pseudo-left: auto, $pseudo-content: '', $pseudo-position: absolute) {
  content: $pseudo-content;
  position: $pseudo-position;
  top: $pseudo-top;
  right: $pseudo-right;
  bottom: $pseudo-bottom;
  left: $pseudo-left;
  width: $pseudo-width;
  height: $pseudo-height; }





