.news-wrapper {
  margin-top: 40px;
  width: 100%;
}

.news {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid rgba(119, 119, 119, 0.4);
  padding-top: 20px;
  transition: all 0.4s ease;
  text-decoration: none;
}

.news:last-child {
  border: none;
}

.news:hover {
  background-color: rgba(230, 244, 252, 0.2);
}

.news:hover .news__caption {
  color: black;
}

.news .news__date {
  margin-bottom: 10px;
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 0.875rem;
  color: #999999;
  line-height: 1.25rem;
  font-style: normal;
}

.news .news__date .date__clock-wrapper {
  vertical-align: top;
  display: inline-block;
  width: 16px;
  min-width: 16px;
  margin-right: 10px;
}

.news .news__date .date__clock-wrapper img {
  display: block;
  width: 100%;
  height: auto;
}

.news .news__caption {
  margin-top: 0;
  margin-bottom: 10px;
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: bold;
  font-size: 1.25rem;
  color: #0063b0;
  line-height: 1.25rem;
  font-style: normal;
  transition: all 0.4s ease;
}

.news .news__caption.news__caption_padding {
  margin-bottom: 20px;
}

.news .news__text {
  margin-bottom: 20px;
  margin-top: 0;
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
}




