.newsBlock-wrapper {
  width: 100%;
}

.newsCaption {
  margin-right: 20px;
  margin-top: 0;
  margin-bottom: 0;
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: bold;
  font-size: 1.5rem;
  color: #000;
  line-height: 2.5rem;
  font-style: normal;
}

.news-list {
  padding: 0;
  margin-top: 10px;
}

.news-list .news-item {
  list-style: none;
  margin-bottom: 20px;
}

.news-list .news-item .news-item__date {
  display: block;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: bold;
  font-size: 0.9375rem;
  color: #999999;
  line-height: 1.25rem;
  font-style: normal;
}

.news-list .news-item .news-item__link {
  display: block;
  text-decoration: none;
  transition: all 0.4s ease;
}

.news-list .news-item .news-item__link:hover {
  background-color: rgba(238, 247, 253, 0.8);
}

.news-list .news-item .news-item__link .news-item__caption {
  margin: 0;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: bold;
  font-size: 0.9375rem;
  color: black;
  line-height: 1.25rem;
  font-style: normal;
}

.news-list .news-item .news-item__link .news-item__text {
  margin: 0;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 0.9375rem;
  color: #555555;
  line-height: 1.25rem;
  font-style: normal;
}




