@import "../../sass/mixin";
@import "../../sass/vars";
@import "../../sass/media";

$lastStep_padding-bottom: 40px;
$lastStep_padding-left: 60px;
.lastStep {
  margin: 0;
  padding: 0 0 $lastStep_padding-bottom $lastStep_padding-left;

  $service-quest__list-item_caption-font-size: 18px;

  $lastStep__caption-span_width: 30px;
  $lastStep__caption-span_height: $lastStep__caption-span_width;
  $lastStep__caption-span_top: 0;
  $lastStep__caption-span_left: -53px;
  $lastStep__caption-span-last-before_width: 15px;
  $lastStep__caption-span-last-before_height: 10px;
  $lastStep__caption-span-last-before_top: 7px;
  $lastStep__caption-span-last-before_left: 7px;
  $lastStep__caption-span-last-before_border-width: 3px;
  $lastStep__caption-span-last-before_border-color: white;
  .lastStep__caption {
    position: relative;
    list-style: none;
    margin: 0 0 20px 0;
    @include font($general-ff-helvetica, $general-font-weight-bold, $service-quest__list-item_caption-font-size);
    & span {
      display: block;
      @include pseudo($lastStep__caption-span_width, $lastStep__caption-span_height, $lastStep__caption-span_top, auto, auto, $lastStep__caption-span_left);
      border-radius: 50%;
      &:before {
        box-sizing: border-box;
        @include pseudo($lastStep__caption-span-last-before_width, $lastStep__caption-span-last-before_height, $lastStep__caption-span-last-before_top, auto, auto, $lastStep__caption-span-last-before_left);
        background-color: transparent;
        transform: rotate(-45deg);
        border-bottom: $lastStep__caption-span-last-before_border-width solid $lastStep__caption-span-last-before_border-color;
        border-left: $lastStep__caption-span-last-before_border-width solid $lastStep__caption-span-last-before_border-color; } } }

  $lastStep__content-font-size: 18px;
  $lastStep__content-font-color: #535B63;
  $lastStep__content-line-height: 28px;
  .lastStep__content {
    list-style: none;
    @include font($general-ff-helvetica, $general-font-weight, $lastStep__content-font-size, $lastStep__content-font-color, $lastStep__content-line-height); } }

@each $name, $color in $color-steps-theme {
  .lastStep.#{$name} .lastStep__caption span {
    background-color: #{$color}; } }



