.article {
  background-color: white;
}

.article pre code {
  background: transparent;
  padding: 0;
}

.article code,
.article pre {
  background: rgba(197, 255, 252, 0.3);
  padding: 10px;
  font-weight: 900;
}

.article table {
  border-collapse: collapse;
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1.0625rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
}

.article td,
.article th {
  padding: 5px 10px;
  border: 1px solid #ccc;
}

.article hr {
  margin: 20px 0;
}

.article p {
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1.0625rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
  margin-bottom: 20px;
}

.article a {
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: bold;
  font-size: 1.0625rem;
  color: #4d92c8;
  line-height: 1.25rem;
  font-style: normal;
}

.article a:hover {
  color: #0063b0;
}

.article h1,
.article h2,
.article h3,
.article h4,
.article h5,
.article h6 {
  margin-top: 0;
  margin-bottom: 20px;
}

.article h1 {
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 300;
  font-size: 2.5rem;
  color: #000;
  line-height: 2.5rem;
  font-style: normal;
}

.article h2 {
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 300;
  font-size: 2rem;
  color: #000;
  line-height: 2.5rem;
  font-style: normal;
}

.article h3 {
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 300;
  font-size: 1.5rem;
  color: #000;
  line-height: 2.5rem;
  font-style: normal;
}

.article h4 {
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 300;
  font-size: 1.25rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
}

.article h5 {
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 300;
  font-size: 1rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
}

.article h6 {
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 300;
  font-size: 0.875rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
}

.article ul {
  padding-left: 20px;
  margin-bottom: 20px;
}

.article ol {
  padding-left: 20px;
  margin-bottom: 20px;
}

.article li {
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1.0625rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
  margin-bottom: 10px;
}

.article img {
  display: block;
  max-width: 800px;
  width: 100%;
  height: 100%;
  margin: 0 auto 20px;
}




