@import "../../sass/mixin";
@import "../../sass/vars";
@import "../../sass/media";

.label {
  display: inline;
  padding: 5px 10px;
  font-size: 75%;
  font-weight: 700;
  line-height: 1;
  color: #fff;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: 4px;
  &--default {
    background-color: color('text','light'); }
  &--primary {
    background-color: color('primary'); }
  &--success {
    background-color: color('success'); }
  &--danger {
    background-color: color('danger'); }
  &--warning {
    background-color: color('warning'); }
  &--info {
    background-color: color('info'); } }



