// Grid
@import "mixins/media";
@import "mixins/grid";
@import "mixins/col";
@import "mixins/offset";
@import "mixins/hidden";

// Breakpoints
$lg: 1220.2px; // 24 columns
$md: 992px; // 12 columns
$sm: 768px; // 6 columns
$xs: $sm - 1px; // 6 columns

// Set the desired number of columns
$columns: 24;

// Set margin between columns
$margin: 20px;

.container .container {
  width: 100%; }



