@import "../../sass/mixin";
@import "../../sass/vars";
@import "../../sass/media";

$info-banner-vertical-step-normal: $general-vertical-step;

$info-banner-before-width: 20px;
$info-banner-before-height: $info-banner-before-width;
$info-banner-before-font-size: $general-font-size-small;

$info-banner-text-color: color('text');
$info-banner-link-color: color('primary');
$info-banner-link-color-hover: color('primary', 'dark');
.info-banner-wrapper {
  margin: 0;
  & .info-banner-text {
    position: relative;
    padding: $info-banner-vertical-step-normal 25px $info-banner-vertical-step-normal 40px;
    margin: 0;
    @include font($general-ff-helvetica, $general-font-weight, $general-font-size, $info-banner-text-color);
    & a {
      color: $info-banner-link-color;
      &:hover {
        color: $info-banner-link-color-hover; } }
    &:before {
      text-align: center;
      min-width: rem-calcF($info-banner-before-width);
      @include pseudo(rem-calcF($info-banner-before-width), rem-calcF($info-banner-before-height), 50%, auto, auto, 0, '!');
      margin-top: rem-calcF(-10px);
      @include font($general-ff-helvetica, $general-font-weight-bold, $info-banner-before-font-size); }
    & .info-banner-caption {
      display: block;
      @include font($general-ff-helvetica, $general-font-weight-bold); } }

  $info-banner-success-color: color('success');
  $info-banner-success-border-color: $info-banner-success-color;
  $info-banner-success-bg-color: color('success', 'light', 'lighter');
  &.info-banner--success {
    background-color: $info-banner-success-bg-color;
    & .info-banner-text:before {
      color: $info-banner-success-color;
      @include border(border, $info-banner-success-border-color, 50%); } }

  $info-banner-warning-color: color('warning');
  $info-banner-warning-border-color: $info-banner-warning-color;
  $info-banner-warning-bg-color: color('warning', 'light', 'lighter');
  &.info-banner--warning {
    background-color: $info-banner-warning-bg-color;
    & .info-banner-text:before {
      color: $info-banner-warning-color;
      @include border(border, $info-banner-warning-border-color, 50%); } }

  $info-banner-danger-bg-color: color('danger', 'light', 'lighter');
  $info-banner-danger-color: color('danger');
  $info-banner-danger-border-color: $info-banner-danger-color;
  &.info-banner--danger {
    background-color: $info-banner-danger-bg-color;
    & .info-banner-text:before {
      color: $info-banner-danger-color;
      @include border(border, $info-banner-danger-border-color, 50%); } } }




