.react-tabs__tab-list {
  display: flex;
  flex-wrap: wrap;
  border-bottom: 1px solid #999999;
  margin: 0 0 20px 0;
  padding: 0;
}

@media (max-width: 768px) {
  .react-tabs__tab-list {
    border-bottom: none;
    justify-content: space-around;
  }
}

@media (min-width: 0) and (max-width: 499px) {
  .react-tabs__tab-list {
    justify-content: center;
    flex-direction: column;
  }
}

.react-tabs__tab {
  position: relative;
  display: block;
  float: left;
  border: 1px solid transparent;
  border-bottom: none;
  margin-bottom: -1px;
  list-style: none;
  padding: 10px 15px;
  cursor: pointer;
  margin-right: 2px;
  color: #0063b0;
}

@media (max-width: 768px) {
  .react-tabs__tab {
    border: 1px solid #999999;
    margin-bottom: 10px;
    border-radius: 4px;
  }
}

@media (min-width: 0) and (max-width: 499px) {
  .react-tabs__tab {
    margin-bottom: 0;
    border-radius: 0;
    border-bottom: none;
  }

  .react-tabs__tab:hover,
  .react-tabs__tab:focus,
  .react-tabs__tab:active {
    border-bottom: none !important;
  }

  .react-tabs__tab:last-child {
    border-bottom: 1px solid #999999 !important;
  }
}

.react-tabs__tab:hover,
.react-tabs__tab:focus,
.react-tabs__tab:active {
  background-color: #eeeeee;
  text-decoration: none;
  outline: none;
  border-bottom: 1px solid #999999;
}

.react-tabs__tab.react-tabs__tab--selected {
  border: 1px solid #999999;
  color: #555555;
  background-color: white;
  cursor: default;
  border-bottom-color: transparent;
  border-radius: 4px 4px 0 0;
}

.react-tabs__tab.react-tabs__tab--selected:hover,
.react-tabs__tab.react-tabs__tab--selected:focus,
.react-tabs__tab.react-tabs__tab--selected:active {
  border-bottom-color: transparent;
  background-color: white;
}

@media (max-width: 768px) {
  .react-tabs__tab.react-tabs__tab--selected {
    background-color: #eeeeee;
    border-radius: 4px;
    border: 1px solid #999999;
  }

  .react-tabs__tab.react-tabs__tab--selected:hover,
  .react-tabs__tab.react-tabs__tab--selected:focus,
  .react-tabs__tab.react-tabs__tab--selected:active {
    border-bottom-color: #999999;
    background-color: #eeeeee;
  }
}

@media (min-width: 0) and (max-width: 499px) {
  .react-tabs__tab.react-tabs__tab--selected {
    border-radius: 0;
    border-bottom: none;
  }
}

.react-tabs__tab.react-tabs__tab--disabled {
  color: #777777;
  cursor: default;
}

.react-tabs__tab.react-tabs__tab--disabled:hover,
.react-tabs__tab.react-tabs__tab--disabled:focus,
.react-tabs__tab.react-tabs__tab--disabled:active {
  background-color: transparent;
  text-decoration: none;
  outline: none;
}

.react-tabs__tab-panel {
  display: none;
}

.react-tabs__tab-panel--selected {
  display: block;
}




