@mixin offset-lg($val: auto) {
  @include media-lg() {
    @if $val == auto or $val == 0 {
      margin-left: auto; }
    @else if $val < 25 {
      margin-left: calc(100% / #{$columns} * #{$val} + #{$margin} / 2) !important; } } }

@mixin offset-md($val: auto) {
  @include media-md() {
    @if $val == auto or $val == 0 {
      margin-left: auto; }
    @else if $val < 25 {
      margin-left: calc(100% / #{$columns} * #{$val} + #{$margin} / 2) !important; } } }

@mixin offset-sm($val: auto) {
  @include media-sm() {
    @if $val == auto or $val == 0 {
      margin-left: auto; }
    @else if $val < 13 {
      margin-left: calc(100% / #{($columns / 2)} * #{$val} + #{($margin - 5)} / 2) !important; } } }

@mixin offset-xs($val: auto) {
  @include media-xs() {
    @if $val == auto or $val == 0 {
      margin-left: auto; }
    @else if $val < 7 {
      margin-left: calc(100% / #{($columns / 4)} * #{$val} + #{($margin - 6)} / 2) !important; } } }



