.banner-logo {
  display: block;
  margin: 0 !important;
  text-decoration: none !important;
}

.banner-logo .banner-logo__prefix {
  display: flex;
  margin: 0;
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: bold;
  font-size: 1.25rem;
  color: #0063b0;
  line-height: 1.25rem;
  font-style: normal;
  text-transform: uppercase;
}

.banner-logo .banner-logo__prefix .banner-logo__suffix {
  margin-left: 5px;
  text-transform: uppercase;
  color: #e90e3f;
}




