@import "../../sass/mixin";
@import "../../sass/vars";
@import "../../sass/media";

$complaint-margin: 20px;
.complaint-wrapper {
  margin-top: 20px;
  & .complaint {
    margin-right: $complaint-margin;
    max-width: 800px;

    $complaint__caption_ff: $general-ff-ekibastuz;
    $complaint__caption_fw: $general-font-weight-bold;
    $complaint__caption_fsz: 20px;
    & .complaint__caption {
      margin-top: 0;
      margin-bottom: $complaint-margin;
      @include font($complaint__caption_ff, $complaint__caption_fw, $complaint__caption_fsz); }

    $complaint__text_ff: $general-ff-ekibastuz;
    & .complaint__text {
      margin-bottom: $complaint-margin;
      @include font($complaint__text_ff); } }
  & a {
    text-decoration: none;
    & button {
      display: flex;
      justify-content: center;
      margin: 40px 0 0 0;
      @include small {
        margin: 20px 0 0 0; }
      & svg {
        margin-left: 10px; } } } }



