@import "../../sass/mixin";
@import '../../sass/vars';
@import '../../sass/media';

$dashboard__items-wrapper-margin-bottom: 80px;
$dashboard__item-border-color: color('text','light','light');
.dashboard__items-wrapper {
  display: flex;
  flex-direction: column;
  margin: 0 0 $dashboard__items-wrapper-margin-bottom 0;
  padding: 0;
  list-style: none;
  & .dashboard__item {
    border-bottom: 1px solid $dashboard__item-border-color;
    transition: $general-transition;
    &:first-child {
      border-top: 1px solid $dashboard__item-border-color; }
    & .dashboard__item-link {
      display: flex;
      width: 100%;
      padding: 10px 0;
      align-items: center;
      cursor: pointer;
      text-decoration: none;
      & .dashboard__col-icon {
        width: 5%;
        text-align: center;
        padding-right: 10px;
        margin-left: 5px;
        @include large {
          width: 8%; }
        @include small {
          padding-right: 5px; }
        & img {
          @include img(); } }
      & .dashboard__col-name-wrapper {
        width: 83%;
        display: flex;
        align-items: center;
        @include large {
          width: 75%; }
        @include small {
          width: 70%;
          flex-direction: column; }
        & .dashboard__col-uslugaName {
          width: 65%;
          padding: 0 20px 0 0;
          @include small {
            width: 100%;
            padding: 0 5px 0 0; }
          & p {
            margin: 0;
            padding: 0;
            word-wrap: break-word;
            @include font($general-ff-ekibastuz); } }
        & .dashboard__col-organizationName {
          width: 35%;
          padding: 0 20px 0 0;
          @include small {
            width: 100%;
            padding: 5px 5px 0 0; }
          & p {
            margin: 0;
            padding: 0;
            word-wrap: break-word;
            @include font($general-ff-ekibastuz, $general-font-weight-bold); } } }
      & .dashboard__col-date {
        width: 12%;
        display: flex;
        align-items: center;
        @include large {
          width: 17%; }
        @include small {
          width: 25%; }
        & span {
          @include font($general-ff-ekibastuz);
          word-break: break-all; } }
      & .arrow {
        margin-right: 10px; } } } }




