@import "../../sass/mixin";
@import "../../sass/vars";
@import "../../sass/media";

.nameCategory {
  margin-bottom: 40px;
  & .card-caption {
    margin-bottom: 20px; }
  & button {
    margin: 0; } }

$titleStep__text_ff: $general-ff-ekibastuz;
$titleStep__text_font-weight: $general-font-weight;
$titleStep__text-bold_font-weight: $general-font-weight-bold;
$titleStep__text_font-size: 17px;
.organization {
  & .organization__caption {
    @include font($titleStep__text_ff, $titleStep__text-bold_font-weight, $titleStep__text_font-size);
    margin: 0 0 10px 0; }
  & .organization__list {
    padding: 0 0 0 20px;
    margin: 0 0 20px 0;
    & .organization__item {
      @include font($titleStep__text_ff, $titleStep__text_font-weight, $titleStep__text_font-size);
      margin-bottom: 10px; } } }

.service {
  margin: 0 0 20px 0; }



