.steps {
  margin: 0;
  padding: 0 0 40px 60px;
  position: relative;
}

.steps:before {
  content: "";
  position: absolute;
  top: 0;
  right: auto;
  bottom: auto;
  left: 20px;
  width: 3px;
  height: 100%;
  border-radius: 4px;
}

.steps .steps__caption {
  list-style: none;
  position: relative;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: bold;
  font-size: 1.125rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
  margin: 0 0 20px 0;
}

.steps .steps__caption span {
  content: "";
  position: absolute;
  top: -5px;
  right: auto;
  bottom: auto;
  left: -53px;
  width: 30px;
  height: 30px;
  display: block;
  padding-left: 7px;
  padding-top: 2px;
  background: white;
  font-size: 17px;
  font-weight: normal;
  border-radius: 50%;
  border: 3px solid;
  outline: 5px solid transparent;
  z-index: 3;
}

.steps .steps__content {
  list-style: none;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1.125rem;
  color: #535B63;
  line-height: 28px;
  font-style: normal;
}

.steps.transportAndDriving:before {
  background: linear-gradient(to bottom, white 0%, #2d7daf 100%);
}

.steps.transportAndDriving .steps__caption span {
  border-color: #2d7daf;
  color: #2d7daf;
}

.steps.primary:before {
  background: linear-gradient(to bottom, white 0%, #6ba9d2 100%);
}

.steps.primary .steps__caption span {
  border-color: #6ba9d2;
  color: #6ba9d2;
}




