@import "../../sass/grid/grid";

@for $i from 1 through 24 {
  .col-lg-#{$i} {
    @include media-lg() {
      @include col-lg($i); } } }

@for $i from 1 through 24 {
  .col-lg-offset-#{$i} {
    @include offset-lg($i); } }

@for $i from 1 through 24 {
  .col-md-#{$i} {
    @include media-md() {
      @include col-md($i); } } }

@for $i from 1 through 24 {
  .col-md-offset-#{$i} {
    @include offset-md($i); } }

@for $i from 1 through 12 {
  .col-sm-#{$i} {
    @include media-sm() {
      @include col-sm($i); } } }

@for $i from 1 through 12 {
  .col-sm-offset-#{$i} {
    @include offset-sm($i); } }

@for $i from 1 through 6 {
  .col-xs-#{$i} {
    @include media-xs() {
      @include col-xs($i); } } }

@for $i from 1 through 6 {
  .col-xs-offset-#{$i} {
    @include offset-xs($i); } }

.hidden-lg {
  @include hidden-lg(); }

.hidden-md {
  @include hidden-md(); }

.hidden-sm {
  @include hidden-sm(); }

.hidden-xs {
  @include hidden-xs(); }



