@import "../../sass/mixin";
@import "../../sass/vars";
@import "../../sass/media";
@import "../Caption/Caption";

.card-caption {
  display: flex;
  align-items: center;
  @include small {
    flex-direction: column;
    align-items: center; }
  & .card-caption__img-wrapper {
    $card-caption-img-width: 72px;
    $card-caption-img-height: 80px;
    $card-caption-img-margin-right: 20px;
    $card-caption-vertical-step-smallX: $general-vertical-step-smallX;

    width: $card-caption-img-width;
    min-width: $card-caption-img-width;
    height: $card-caption-img-height;
    margin: 0 $card-caption-img-margin-right 0 0;
    @include xsmall {
      margin: 0 0 $card-caption-vertical-step-smallX 0; }
    & img {
      @include img(); } }
  & .card-caption__name {
    $card-caption__name_font-size: $general-font-size-normal;
    $card-caption__name_line-height: $general-font-line-height;
    $card-caption__name_ff: $general-ff-ekibastuz;
    $card-caption__name_font-weight: $general-font-weight-bold;
    $card-caption__name_font-color: $general-font-color;

    @include font($card-caption__name_ff, $card-caption__name_font-weight, $card-caption__name_font-size, $card-caption__name_font-color, $card-caption__name_line-height);
    margin: 0;
    @include xsmall {
      text-align: center; } }
  &.large,&-lg {
    margin-bottom: 20px;
    & .card-caption__name {
      @include font($base-caption-ff, $general-font-weight-light, 42px, $general-font-color, 48px); } }
  &-sm {
    & .card-caption__img-wrapper {
      width: 47px;
      min-width: 47px;
      height: 52px;
      margin-right: 10px; }
    & .card-caption__name {
      font-size: 16px; } }
  &-xs {
    & .card-caption__img-wrapper {
      width: 29px;
      min-width: 29px;
      height: 32px;
      margin-right: 5px; }
    & .card-caption__name {
      font-size: 15px; } } }



