@import "../../sass/mixin";
@import "../../sass/vars";
@import "../../sass/media";

.newsBlock-wrapper {
  width: 100%; }

$newsCaption-ff: $general-ff-ekibastuz;
$newsCaption-font-weight: $general-font-weight-bold;
$newsCaption-font-size: 24px;
$newsCaption-font-color: $general-font-color;
$newsCaption-line-height: $general-font-line-height-large;
.newsCaption {
  margin-right: 20px;
  margin-top: 0;
  margin-bottom: 0;
  @include font($newsCaption-ff, $newsCaption-font-weight, $newsCaption-font-size, $newsCaption-font-color, $newsCaption-line-height); }

$news-item__margin-top: 10px;

$news-item__date-ff: $general-ff-helvetica;
$news-item__date-font-weight: $general-font-weight-bold;
$news-item__date-font-size: 15px;
$news-item__date-color: color('text', 'lightGray');

$news-item__caption-ff: $news-item__date-ff;
$news-item__caption-font-weight: $general-font-weight-bold;
$news-item__caption-font-size: $news-item__date-font-size;
$news-item__caption-color: color('black');

$news-item__text-ff: $news-item__date-ff;
$news-item__text-font-weight: $general-font-weight;
$news-item__text-font-size: $news-item__date-font-size;
$news-item__text-color: color('text');
.news-list {
  padding: 0;
  margin-top: $news-item__margin-top;
  & .news-item {
    list-style: none;
    margin-bottom: 20px;
    & .news-item__date {
      display: block;
      @include font($news-item__date-ff, $news-item__date-font-weight, $news-item__date-font-size, $news-item__date-color); }
    & .news-item__link {
      display: block;
      text-decoration: none;
      transition: $general-transition;
      &:hover {
        background-color: color('secondary', 'light', 'darker'); }
      & .news-item__caption {
        margin: 0;
        @include font($news-item__caption-ff, $news-item__caption-font-weight, $news-item__caption-font-size, $news-item__caption-color); }
      & .news-item__text {
        margin: 0;
        @include font($news-item__text-ff, $news-item__text-font-weight, $news-item__text-font-size, $news-item__text-color); } } } }



