.lastStep {
  margin: 0;
  padding: 0 0 40px 60px;
}

.lastStep .lastStep__caption {
  position: relative;
  list-style: none;
  margin: 0 0 20px 0;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: bold;
  font-size: 1.125rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
}

.lastStep .lastStep__caption span {
  display: block;
  content: "";
  position: absolute;
  top: 0;
  right: auto;
  bottom: auto;
  left: -53px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

.lastStep .lastStep__caption span:before {
  box-sizing: border-box;
  content: "";
  position: absolute;
  top: 7px;
  right: auto;
  bottom: auto;
  left: 7px;
  width: 15px;
  height: 10px;
  background-color: transparent;
  transform: rotate(-45deg);
  border-bottom: 3px solid white;
  border-left: 3px solid white;
}

.lastStep .lastStep__content {
  list-style: none;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1.125rem;
  color: #535B63;
  line-height: 28px;
  font-style: normal;
}

.lastStep.transportAndDriving .lastStep__caption span {
  background-color: #2d7daf;
}

.lastStep.primary .lastStep__caption span {
  background-color: #6ba9d2;
}




