@import "../../sass/mixin";
@import "../../sass/vars";
@import "../../sass/media";

.NewYearBanner {
  width: 100%;
  display: block;
  position: relative;
  height: 100px;
  background: url("./christmass-banner.png") no-repeat center center;
  cursor: pointer;
  & .NewYearBanner__btn-wrapper {
    position: absolute;
    top: 50%;
    margin-top: 23px;
    left: 70%;
    margin-left: -107px;
    @include large {
      left: 50%; }
    & .button.button-default {
      margin: 0;
      background-color: rgba(255,255,255,0.6);
      font-family: $general-ff-ekibastuz;
      @include large {
        background-color: rgba(255,255,255, 1); } }
    & .NewYearBanner__decor {
      display: block;
      position: absolute;
      top: -4px;
      left: 4px;
      right: 0;
      height: 22px;
      z-index: 2;
      background: url(./gerlyanda.png) 0 0 repeat-x;
      animation: gerlyanda 1500ms infinite step-end; } } }

@keyframes gerlyanda {
  0%, 33% {
    background-position: 0 0; }
  33.1%, 66% {
    background-position: 0 -24px; }
  66.1%, 100% {
    background-position: 0 -48px; } }



