@import "../../sass/mixin";
@import "../../sass/vars";
@import "../../sass/media";

.button-wrapper {
  display: block;
  &__left {
    text-align: left; }
  &__center {
    text-align: center; }
  &__right {
    text-align: right; } }

$btn-ff: $general-ff-helvetica;
$btn-font-weight: $general-font-weight;

$btn-font-size-lg: 18px;
$btn-font-size: $general-font-size;
$btn-font-size-sm: 15px;
$btn-font-size-xs: $general-font-size-small;

$btn-padding-horizontal: 15px;
$btn-padding-vertical: 12px;
$btn-padding-horizontal-lg: 30px;
$btn-padding-vertical-lg: 16px;
$btn-padding-horizontal-sm: 10px;
$btn-padding-vertical-sm: 5px;
$btn-padding-horizontal-xs: 6px;
$btn-padding-vertical-xs: 3px;

$btn-margin: 5px 0;
$btn-border: 1px solid transparent;
$btn-border-radius: $general-border-radius;
$btn-blackout: 10%;
$btn-blackout-active: 20%;
$btn-mix-black: color('black');

.button {
  position: relative;
  @include inline-block();
  border: $btn-border;
  border-radius: $btn-border-radius;
  padding: $btn-padding-vertical $btn-padding-horizontal;
  margin: $btn-margin;
  @include font($btn-ff, $btn-font-weight, $btn-font-size);
  transition: $general-transition;
  cursor: pointer;
  white-space: nowrap;
  touch-action: manipulation;
  &:focus {
    outline: none; }

  &[disabled] {
    cursor: not-allowed;
    opacity: .65;
    filter: alpha(opacity=65);
    box-shadow: none; }

  &.button-block {
    display: block;
    width: 100%; }

  &.button-default {
    $btn-default_color: color('text', 'dark');
    $btn-default_bg-color: color('white');
    $btn-default_border-color: color('text','light', 'light');

    color: $btn-default_color;
    background-color: $btn-default_bg-color;
    border-color: $btn-default_border-color;
    &:hover, &:focus {
      background-color: mix($btn-mix-black, $btn-default_bg-color, $btn-blackout); }
    &:active {
      background-color: mix($btn-mix-black, $btn-default_bg-color, $btn-blackout-active); }
    &.active {
      background-color: mix($btn-mix-black, $btn-default_bg-color, $btn-blackout-active); } }

  &.button-primary {
    $btn-primary_color: color('white');
    $btn-primary_bg-color: color('primary');
