    color: $btn-primary_color;
    background-color: $btn-primary_bg-color;
    border-color: $btn-primary_border-color;
    &:hover, &:focus {
      background-color: mix($btn-mix-black, $btn-primary_bg-color, $btn-blackout); }
    &:active {
      background-color: mix($btn-mix-black, $btn-primary_bg-color, $btn-blackout-active); }
    &.active {
      background-color: mix($btn-mix-black, $btn-primary_bg-color, $btn-blackout-active); } }

  &.button-info {
    $btn-info_color: color('white');
    $btn-info_bg-color: color('info');
    $btn-info_border-color: color('info','dark', 'dark');

    color: $btn-info_color;
    background-color: $btn-info_bg-color;
    border-color: $btn-info_border-color;
    &:hover, &:focus {
      background-color: mix($btn-mix-black, $btn-info_bg-color, $btn-blackout); }
    &:active {
      background-color: mix($btn-mix-black, $btn-info_bg-color, $btn-blackout-active); }
    &.active {
      background-color: mix($btn-mix-black, $btn-info_bg-color, $btn-blackout-active); } }

  &.button-success {
    $btn-success_color: color('white');
    $btn-success_bg-color: color('success');
    $btn-success_border-color: color('success', 'dark', 'dark');

    color: $btn-success_color;
    background-color: $btn-success_bg-color;
    border-color: $btn-success_border-color;
    &:hover, &:focus {
      background-color: mix($btn-mix-black, $btn-success_bg-color, $btn-blackout); }
    &:active {
      background-color: mix($btn-mix-black, $btn-success_bg-color, $btn-blackout-active); }
    &.active {
      background-color: mix($btn-mix-black, $btn-success_bg-color, $btn-blackout-active); } }

  &.button-warning {
    $btn-warning_color: color('white');
    $btn-warning_bg-color: color('warning');
    $btn-warning_border-color: color('warning','dark', 'dark');

    color: $btn-warning_color;
    background-color: $btn-warning_bg-color;
    border-color: $btn-warning_border-color;
    &:hover, &:focus {
      background-color: mix($btn-mix-black, $btn-warning_bg-color, $btn-blackout); }
    &:active {
      background-color: mix($btn-mix-black, $btn-warning_bg-color, $btn-blackout-active); }
    &.active {
      background-color: mix($btn-mix-black, $btn-warning_bg-color, $btn-blackout-active); } }

  &.button-danger {
    $btn-danger_color: color('white');
    $btn-danger_bg-color: color('danger');
    $btn-danger_border-color: color('danger', 'dark', 'dark');

    color: $btn-danger_color;
    background-color: $btn-danger_bg-color;
    border-color: $btn-danger_border-color;
    &:hover, &:focus {
      background-color: mix($btn-mix-black, $btn-danger_bg-color, $btn-blackout); }
    &:active {
      background-color: mix($btn-mix-black, $btn-danger_bg-color, $btn-blackout-active); }
    &.active {
      background-color: mix($btn-mix-black, $btn-danger_bg-color, $btn-blackout-active); } }

  &.button-link {
    $btn-default_color: color('primary');
    $btn-default_bg-color: transparent;
    $btn-default_border-color: transparent;

    color: $btn-default_color;
    background-color: $btn-default_bg-color;
    border-color: $btn-default_border-color;
    border-radius: 0;
    box-shadow: none;
    &:hover, &:focus {
      background-color: $btn-default_bg-color;
      color: mix($btn-mix-black, $btn-default_color, $btn-blackout);
      text-decoration: underline; }
    &:active {
      background-color: $btn-default_bg-color;
      color: mix($btn-mix-black, $btn-default_color, $btn-blackout-active);
      text-decoration: underline; }
    &.active {
      background-color: $btn-default_bg-color;
      color: mix($btn-mix-black, $btn-default_color, $btn-blackout-active);
      text-decoration: underline; } }

  &.button-lg {
    font-size: rem-calcF($btn-font-size-lg);
    padding: $btn-padding-vertical-lg $btn-padding-horizontal-lg; }
  &.button-sm {
    font-size: rem-calcF($btn-font-size-sm);
    padding: $btn-padding-vertical-sm $btn-padding-horizontal-sm; }
  &.button-xs {
    font-size: rem-calcF($btn-font-size-xs);
    padding: $btn-padding-vertical-xs $btn-padding-horizontal-xs; }

  &.button-arrow-right {
    $btn-arrow-right-padding: 25px;
    $btn-arrow-left-padding: 15px;
    $btn-arrow-vertical-padding: 8px;
    $btn-arrow-margin-top: -5px;
    $btn-arrow-color: color('white');
    $btn-arrow-bg-color: color('primary');
    $btn-arrow-before-border-width: 2px;
    $btn-arrow-before-border-radius: 2px;
    $btn-arrow-before-right: 10px;
    $btn-arrow-before-width: 10px;
    $btn-arrow-before-height: $btn-arrow-before-width;

    padding: $btn-arrow-vertical-padding $btn-arrow-right-padding $btn-arrow-vertical-padding $btn-arrow-left-padding;

    color: $btn-arrow-color;
    background-color: $btn-arrow-bg-color;
    &:before {
      @include pseudo($btn-arrow-before-width, $btn-arrow-before-height, 50%, $btn-arrow-before-right, auto, auto);
      margin-top: $btn-arrow-margin-top;
      border-bottom: $btn-arrow-before-border-width solid $btn-arrow-color;
      border-right: $btn-arrow-before-border-width solid $btn-arrow-color;
      border-radius: $btn-arrow-before-border-radius;
      transform: rotate(-45deg); }
    &:hover, &:focus {
      background-color: mix($btn-mix-black, $btn-arrow-bg-color, $btn-blackout); }
    &:active {
      background-color: mix($btn-mix-black, $btn-arrow-bg-color, $btn-blackout-active); }
    &.active {
      background-color: mix($btn-mix-black, $btn-arrow-bg-color, $btn-blackout-active); }
    &.button-lg {
      $btn-arrow-right-padding-lg: 44px;
      $btn-arrow-before-margin-top-lg: -6px;
      $btn-arrow-before-width-lg: 12px;
      $btn-arrow-before-right-lg: 18px;
      $btn-arrow-before-height-lg: $btn-arrow-before-width-lg;

      padding: $btn-padding-vertical-lg $btn-arrow-right-padding-lg $btn-padding-vertical-lg $btn-padding-horizontal-lg;
      &:before {
        right: $btn-arrow-before-right-lg;
        margin-top: $btn-arrow-before-margin-top-lg;
        width: $btn-arrow-before-width-lg;
        height: $btn-arrow-before-height-lg; } }

    &.button-sm {
      $btn-arrow-right-padding-sm: 20px;
      $btn-arrow-before-margin-top-sm: -4px;
      $btn-arrow-before-right-sm: 10px;
      $btn-arrow-before-width-sm: 8px;
      $btn-arrow-before-height-sm: $btn-arrow-before-width-sm;

      padding: $btn-padding-vertical-sm $btn-arrow-right-padding-sm $btn-padding-vertical-sm $btn-padding-horizontal-sm;
      &:before {
        right: $btn-arrow-before-right-sm;
        margin-top: $btn-arrow-before-margin-top-sm;
        width: $btn-arrow-before-width-sm;
        height: $btn-arrow-before-height-sm; } }

    &.button-xs {
      $btn-arrow-right-padding-xs: 16px;
      $btn-arrow-before-margin-top-xs: -4px;
      $btn-arrow-before-right-xs: 6px;
      $btn-arrow-before-width-xs: 8px;
      $btn-arrow-before-height-xs: $btn-arrow-before-width-xs;

      padding: $btn-padding-vertical-xs $btn-arrow-right-padding-xs $btn-padding-vertical-xs $btn-padding-horizontal-xs;
      &:before {
        right: $btn-arrow-before-right-xs;
        margin-top: $btn-arrow-before-margin-top-xs;
        width: $btn-arrow-before-width-xs;
        height: $btn-arrow-before-height-xs; } } }

  &.button-icon {
    display: flex;
    & span {
      margin-left: 10px; } }

  &.button-arrow-left {
    $btn-arrow-left_right-padding: 15px;
    $btn-arrow-left_left-padding: 25px;
    $btn-arrow-left_vertical-padding: 8px;
    $btn-arrow-left_margin-top: -5px;
    $btn-arrow-left_color: color('text');
    $btn-arrow-left_bg-color: color('white');
    $btn-arrow-left_border-color: color('text', 'light', 'dark');
    $btn-arrow-left_before-border-width: 2px;
    $btn-arrow-left_before-border-radius: 2px;
    $btn-arrow-left_before-right: 10px;
    $btn-arrow-left_before-width: 10px;
    $btn-arrow-left_before-height: $btn-arrow-left_before-width;

    padding: $btn-arrow-left_vertical-padding $btn-arrow-left_right-padding $btn-arrow-left_vertical-padding $btn-arrow-left_left-padding;
    border-color: $btn-arrow-left_border-color;
    color: $btn-arrow-left_color;
    background-color: $btn-arrow-left_bg-color;
    &:before {
      @include pseudo($btn-arrow-left_before-width, $btn-arrow-left_before-height, 50%, auto, auto, $btn-arrow-left_before-right);
      margin-top: $btn-arrow-left_margin-top;
      border-bottom: $btn-arrow-left_before-border-width solid $btn-arrow-left_color;
      border-right: $btn-arrow-left_before-border-width solid $btn-arrow-left_color;
      border-radius: $btn-arrow-left_before-border-radius;
      transform: rotate(135deg); }
    &:hover, &:focus {
      background-color: mix($btn-mix-black, $btn-arrow-left_bg-color, $btn-blackout); }
    &:active {
      background-color: mix($btn-mix-black, $btn-arrow-left_bg-color, $btn-blackout-active); }
    &.active {
      background-color: mix($btn-mix-black, $btn-arrow-left_bg-color, $btn-blackout-active); }
    &.button-lg {
      $btn-arrow-left_left-padding-lg: 44px;
      $btn-arrow-left_before-margin-top-lg: -6px;
      $btn-arrow-left_before-width-lg: 12px;
      $btn-arrow-left_before-right-lg: 18px;
      $btn-arrow-left_before-height-lg: $btn-arrow-left_before-width-lg;

      padding: $btn-padding-vertical-lg $btn-padding-horizontal-lg $btn-padding-vertical-lg $btn-arrow-left_left-padding-lg;
      &:before {
        left: $btn-arrow-left_before-right-lg;
        margin-top: $btn-arrow-left_before-margin-top-lg;
        width: $btn-arrow-left_before-width-lg;
        height: $btn-arrow-left_before-height-lg; } }

    &.button-sm {
      $btn-arrow-left_left-padding-sm: 20px;
      $btn-arrow-left_before-margin-top-sm: -4px;
      $btn-arrow-left_before-right-sm: 10px;
      $btn-arrow-left_before-width-sm: 8px;
      $btn-arrow-left_before-height-sm: $btn-arrow-left_before-width-sm;

      padding: $btn-padding-vertical-sm $btn-padding-horizontal-sm $btn-padding-vertical-sm $btn-arrow-left_left-padding-sm;
      &:before {
        left: $btn-arrow-left_before-right-sm;
        margin-top: $btn-arrow-left_before-margin-top-sm;
        width: $btn-arrow-left_before-width-sm;
        height: $btn-arrow-left_before-height-sm; } }

    &.button-xs {
      $btn-arrow-left_left-padding-xs: 16px;
      $btn-arrow-left_before-margin-top-xs: -4px;
      $btn-arrow-left_before-right-xs: 6px;
      $btn-arrow-left_before-width-xs: 8px;
      $btn-arrow-left_before-height-xs: $btn-arrow-left_before-width-xs;

      padding: $btn-padding-vertical-xs $btn-padding-horizontal-xs $btn-padding-vertical-xs $btn-arrow-left_left-padding-xs;
      &:before {
        left: $btn-arrow-left_before-right-xs;
        margin-top: $btn-arrow-left_before-margin-top-xs;
        width: $btn-arrow-left_before-width-xs;
        height: $btn-arrow-left_before-height-xs; } } } }

$addButton-width: 26px;
$addButton-height: $addButton-width;
$addButton-border-width: 2px;
$addButton-margin-right: 10px;
$addButton-color: color('success');
$addButton-pseudo-width: 14px;
$addButton-pseudo-height: 2px;
$addButton-pseudo-top: 50%;
$addButton-pseudo-left: $addButton-pseudo-top;
.addButton {
  position: relative;
  @include inline-block(bottom);
  min-width: $addButton-width;
  width: $addButton-width;
  height: $addButton-height;
  margin-right: $addButton-margin-right;
  border-radius: 50%;
  border: $addButton-border-width solid $addButton-color;
  text-align: center;
  &:after, &:before {
    @include pseudo($addButton-pseudo-width, $addButton-pseudo-height, $addButton-pseudo-top, auto, auto, $addButton-pseudo-left);
    display: block;
    background: $addButton-color;
    margin-left: -$addButton-pseudo-width/2;
    margin-top: -$addButton-pseudo-height/2; }
  &:before {
    transform: rotate(90deg); } }

$controlBtn-margin-top: 15px;
$controlBtn-margin-right: 10px;
$controlBtn-width: 30px;
$controlBtn-height: $controlBtn-width;
.deleteButton, .dwButton, .upButton {
  position: relative;
  min-width: $controlBtn-width;
  width: $controlBtn-width;
  height: $controlBtn-height;
  @include inline-block(bottom);
  padding: 0;
  margin: $controlBtn-margin-top $controlBtn-margin-right 0 0;
  text-align: center;
  background-size: cover;
  cursor: pointer;
  transition: $general-transition;
  @include small {
    margin: 0 10px 20px 0; }
  &:hover, &:focus {
    opacity: 0.5; }
  &:active {
    opacity: 0.7; } }

.deleteButton {
  background-image: url("./icon/btn_cancel.svg"); }

.dwButton {
  background-image: url("./icon/btn_dw.svg"); }

.upButton {
  background-image: url("./icon/btn_up.svg"); }

.btn-wrapper {
  display: flex;
  align-items: center; }



