@import "../../sass/mixin";
@import "../../sass/vars";
@import "../../sass/media";

.download-files-wrapper {
  display: flex;
  &-vertical {
    flex-direction: column; } }

$list-item__img-width: 45px;
$list-item__text-size: 20px;
$list-item__link-size: 16px;
$list-item__link-color: color('primary');
$list-item__link-color-hover: color('primary', 'dark');
$list-item__text-family: $general-ff-ekibastuz;
$list-item__text-weight: $general-font-weight;
.download-files__item {
  display: flex;
  width: 100%;
  margin-bottom: 20px;
  text-decoration: none;
  &:focus, &:active {
    text-decoration: none; }
  &:hover .item__text .item__download {
    color: $list-item__link-color-hover;
    text-decoration: underline; }
  & .item__img {
    width: rem-calcF($list-item__img-width);
    min-width: rem-calcF($list-item__img-width);
    margin-right: 10px;
    & img {
      @include img(); } }
  & .item__text {
    & .item__name {
      display: block;
      @include font($list-item__text-family, $list-item__text-weight, $list-item__text-size);
      margin: 0;
      word-break: break-all; }
    & .item__download {
      display: block;
      margin-top: 10px;

      @include font($list-item__text-family, $list-item__text-weight, $list-item__link-size, $list-item__link-color); } }
  &-lg {
    & .item__img {
      width: rem-calcF(60px);
      min-width: rem-calcF(60px);
      margin-right: 20px; }
    & .item__text {
      & .item__name {
        font-size: 24px;
        line-height: 2.5rem; }
      & .item__download {
        font-size: 20px; } } }
  &-sm {
    & .item__img {
      width: rem-calcF(30px);
      min-width: rem-calcF(30px);
      margin-right: 10px; }
    & .item__text {
      & .item__name {
        font-size: 16px; }
      & .item__download {
        margin-top: 3px;
        font-size: 14px; } } } }



