@import "../../sass/mixin";
@import "../../sass/vars";
@import "../../sass/media";

$titleStep__text_ff: $general-ff-ekibastuz;
$titleStep__text_font-weight: $general-font-weight;
$titleStep__text-bold_font-weight: $general-font-weight-bold;
$titleStep__text_font-size: 17px;
.titleStep {
  & .titleStep__text {
    @include font($titleStep__text_ff, $titleStep__text_font-weight, $titleStep__text_font-size);
    &:first-child {
      margin-top: 0; } }
  & .titleStep__text-bold {
    @include font($titleStep__text_ff, $titleStep__text-bold_font-weight, $titleStep__text_font-size);
    margin-right: 10px; } }


$titleStep__list-padding_vertical: 20px;
$titleStep__list-padding_left: 60px;
.titleStep__list {
  margin: 0;
  padding: $titleStep__list-padding_vertical 0 $titleStep__list-padding_vertical $titleStep__list-padding_left;

  $list__caption_font-size: 24px;
  $list__caption-padding-bottom: 20px;
  & .list__caption {
    position: relative;
    @include font($general-ff-helvetica, $general-font-weight-bold, $list__caption_font-size);
    list-style: none;
    padding-bottom: $list__caption-padding-bottom;

    $list__caption-after_width: 15px;
    $list__caption-after_height: $list__caption-after_width;
    $list__caption-after_bottom: 10px;
    $list__caption-after_left: -46px;
    $list__caption-after_bg-color: transparent;
    $list__caption-after_border-width: 3px;
    &:after {
      box-sizing: border-box;
      @include pseudo($list__caption-after_width, $list__caption-after_height, auto, auto, $list__caption-after_bottom, $list__caption-after_left);
      border-bottom: $list__caption-after_border-width solid;
      border-right: $list__caption-after_border-width solid;
      transform: rotate(45deg);
      background-color: $list__caption-after_bg-color; }

    $list__caption-before_width: 3px;
    $list__caption-before_height: 100%;
    $list__caption-before_top: -10px;
    $list__caption-before_left: -40px;
    $list__caption-before_border-radius: $general-border-radius;
    &:before {
      @include pseudo($list__caption-before_width, $list__caption-before_height, $list__caption-before_top, auto, auto, $list__caption-before_left);
      border-radius: $list__caption-before_border-radius; } }
  & .list__content {
    list-style: none;
    margin-bottom: 20px;
    @include font($titleStep__text_ff, $titleStep__text_font-weight, $titleStep__text_font-size); } }

$color-theme: (transportAndDriving: $transportAndDriving, primary: color('primary', 'light'));
@each $name, $color in $color-theme {
  .titleStep__list.#{$name} .list__caption {
    &:after {
      border-color: #{$color}; } }
  .titleStep__list.#{$name} .list__caption {
    &:before {
      background-color: #{$color}; } } }



