@import "../../sass/mixin";
@import "../../sass/vars";
@import "../../sass/media";

$thanks-vertical-step: $general-vertical-step;

$thanks__link-color: color('primary');
$thanks__link-color-hover: color('primary','light');

$thanks-arrow-margin-right-btn: 20px;
$thanks-arrow-margin-left-btn: 100px;
$thanks-arrow-margin-bottom-btn: $general-vertical-step-smallX;
.thanks__caption-wrapper {
  display: flex;
  align-items: center;
  margin-bottom: $thanks-vertical-step; }

.thanks__btn-wrapper {
  display: flex;
  align-items: center;
  margin: $thanks-vertical-step 0;
  @include small {
    flex-direction: column;
    align-items: flex-start; }
  & .thanks__link {
    @include font($general-ff-helvetica, $general-font-weight, $general-font-size, $thanks__link-color);
    &:hover {
      color: $thanks__link-color-hover; } }
  & .button {
    margin: 0 $thanks-arrow-margin-right-btn 0 $thanks-arrow-margin-left-btn;
    @include small {
      margin: 0 0 $thanks-arrow-margin-bottom-btn 0; } } }

$thanks__ok-width: 80px;
$thanks__ok-width-small: 40px;
$thanks__ok-height: $thanks__ok-width;
$thanks__ok-height-small: $thanks__ok-width-small;
$thanks__ok-border-width: 8px;
$thanks__ok-border-width-small: 4px;
$thanks__ok-border-color: color('white', 'dark');
$thanks__ok-border-radius: 50%;
$thanks__ok-margin-right: $general-vertical-step;

$thanks__ok-after_width: 8px;
$thanks__ok-after_height: 30px;
$thanks__ok-after_top: 17px;
$thanks__ok-after_left: 35px;
$thanks__ok-after_border-radius: 10px;
$thanks__ok-after_bg-color: color('success');

$thanks__ok-before_width: 8px;
$thanks__ok-before_height: 23px;
$thanks__ok-before_top: 25px;
$thanks__ok-before_left: 20px;
$thanks__ok-before_border-radius: 10px;
$thanks__ok-before_bg-color: $thanks__ok-after_bg-color;

.thanks__ok {
  position: relative;
  width: $thanks__ok-width;
  height: $thanks__ok-height;
  min-width: $thanks__ok-width;
  border: $thanks__ok-border-width solid $thanks__ok-border-color;
  border-radius: $thanks__ok-border-radius;
  margin-right: $thanks__ok-margin-right;
  @include small {
    width: $thanks__ok-width-small;
    height: $thanks__ok-height-small;
    min-width: $thanks__ok-width-small;
    border-width: $thanks__ok-border-width-small; }
  &:after {
    @include pseudo($thanks__ok-after_width, $thanks__ok-after_height, $thanks__ok-after_top, auto, auto, $thanks__ok-after_left);
    transform: rotate(45deg);
    background-color: $thanks__ok-after_bg-color;
    border-radius: $thanks__ok-after_border-radius;
    @include small {
      left: 18px;
      top: 8px;
      width: 4px;
      height: 15px; } }
  &:before {
    @include pseudo($thanks__ok-before_width, $thanks__ok-before_height, $thanks__ok-before_top, auto, auto, $thanks__ok-before_left);
    transform: rotate(-45deg);
    background-color: $thanks__ok-after_bg-color;
    border-radius: $thanks__ok-before_border-radius;
    @include small {
      left: 9px;
      top: 12px;
      width: 4px;
      height: 12px; } } }

$thanks__caption-font-size: $general-font-size-large;
$thanks__caption-lh: $general-font-line-height-large;
.thanks__caption {
  margin: 0;
  padding: 0;
  @include font($general-ff-ekibastuz, $general-font-weight-bold, $thanks__caption-font-size, $general-font-color, $thanks__caption-lh);
  @include small {
    line-height: 1.25rem; } }

$thanks__info-margin-left: $thanks-arrow-margin-left-btn;

$thanks__info-text-margin-bottom: $general-vertical-step;
$thanks__info-text-font-size: $general-font-size-normal;
$thanks__info-text-line-height: $general-font-line-height;

$thanks__info-phone-link-font-size: $general-font-size;
$thanks__info-phone-link-line-height: $general-font-line-height;
$thanks__info-phone-link-color: color('primary');
$thanks__info-phone-link-color-hover: color('primary', 'light');
$thanks__info-phone-link-width: 200px;
.thanks__info {
  margin-left: $thanks__info-margin-left;
  @include small {
    margin-left: 0; }
  & .thanks__info-text {
    @include font($general-ff-helvetica, $general-font-weight-light, $thanks__info-text-font-size, $general-font-color, $thanks__info-text-line-height);
    margin-bottom: $thanks__info-text-margin-bottom; }
  & .thanks__info-phone-wrapper, .thanks__info-email-wrapper {
    display: flex;
    margin-bottom: $general-vertical-step;
    & .thanks__info-phone-link, .thanks__info-email-text {
      @include font($general-ff-helvetica, $general-font-weight, $thanks__info-phone-link-font-size, $general-font-color, $thanks__info-phone-link-line-height); }
    & .thanks__info-phone-link {
      color: $thanks__info-phone-link-color;
      &:hover {
        color: $thanks__info-phone-link-color-hover; } }
    & .thanks__info-phone, .thanks__info-email {
      @include inline-block();
      min-width: $thanks__info-phone-link-width;
      @include font($general-ff-helvetica, $general-font-weight-bold, $thanks__info-phone-link-font-size, $general-font-color, $thanks__info-phone-link-line-height);
      @include small {
        min-width: 180px; } } } }





