@import "../../sass/mixin";
@import "../../sass/vars";
@import "../../sass/media";

.react-tabs__tab-list {
  display: flex;
  flex-wrap: wrap;
  border-bottom: 1px solid color('text', 'lightGray');
  margin: 0 0 20px 0;
  padding: 0;
  @include medium {
    border-bottom: none;
    justify-content: space-around; }
  @include small {
    justify-content: center;
    flex-direction: column; } }

.react-tabs__tab {
  position: relative;
  display: block;
  float: left;
  border: 1px solid transparent;
  border-bottom: none;
  margin-bottom: -1px;
  list-style: none;
  padding: 10px 15px;
  cursor: pointer;
  margin-right: 2px;
  color: color('primary');
  @include medium {
    border: 1px solid color('text', 'lightGray');
    margin-bottom: 10px;
    border-radius: 4px; }
  @include small {
    margin-bottom: 0;
    border-radius: 0;
    border-bottom: none;
    &:hover, &:focus, &:active {
      border-bottom: none!important; }
    &:last-child {
      border-bottom: 1px solid color('text', 'lightGray')!important; } }


  &:hover, &:focus, &:active {
    background-color: color('text', 'lighter');
    text-decoration: none;
    outline: none;
    border-bottom: 1px solid color('text', 'lightGray'); }

  &.react-tabs__tab--selected {
    border: 1px solid color('text', 'lightGray');
    color: color('text');
    background-color: white;
    cursor: default;
    border-bottom-color: transparent;
    border-radius: 4px 4px 0 0;
    &:hover, &:focus, &:active {
      border-bottom-color: transparent;
      background-color: white; }
    @include medium {
      background-color: color('text', 'lighter');
      border-radius: 4px;
      border: 1px solid color('text', 'lightGray');
      &:hover, &:focus, &:active {
        border-bottom-color: color('text', 'lightGray');
        background-color: color('text', 'lighter'); } }
    @include small {
      border-radius: 0;
      border-bottom: none; } }


  &.react-tabs__tab--disabled {
    color: color('text', 'light');
    cursor: default;
    &:hover, &:focus, &:active {
      background-color: transparent;
      text-decoration: none;
      outline: none; } } }



.react-tabs__tab-panel {
  display: none;
  &--selected {
    display: block; } }



