.octagon-wrapper {
  width: 100%;
  text-align: center;
}

.octagon-wrapper.octagon-wrapper__lg .octagon {
  width: 124px;
  height: 124px;
}

.octagon-wrapper.octagon-wrapper__lg .octagon svg {
  width: 124px;
  height: 124px;
  transform-origin: 0 62px;
}

.octagon-wrapper.octagon-wrapper__lg h3 {
  font-size: 26px;
}

.octagon-wrapper.octagon-wrapper__lg p {
  font-size: 22px;
}

.octagon-wrapper.octagon-wrapper__sm .octagon {
  margin: 10px auto;
  width: 84px;
  height: 84px;
}

.octagon-wrapper.octagon-wrapper__sm .octagon svg {
  width: 84px;
  height: 84px;
  transform-origin: 0 42px;
}

.octagon-wrapper.octagon-wrapper__sm h3 {
  margin: 10px 0;
  font-size: 20px;
}

.octagon-wrapper.octagon-wrapper__sm p {
  margin: 10px 0;
  font-size: 18px;
}

.octagon-wrapper.octagon-wrapper__xs .octagon {
  margin: 10px auto;
  width: 64px;
  height: 64px;
}

.octagon-wrapper.octagon-wrapper__xs .octagon svg {
  width: 64px;
  height: 64px;
  transform-origin: 0 32px;
}

.octagon-wrapper.octagon-wrapper__xs h3 {
  margin: 10px 0;
  font-size: 18px;
}

.octagon-wrapper.octagon-wrapper__xs p {
  margin: 10px 0;
  font-size: 16px;
}

.octagon-wrapper h3 {
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: bold;
  font-size: 1.5rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
  margin: 20px 0;
}

.octagon-wrapper p {
  font-family: "Ekibastuz", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1.25rem;
  color: #000;
  line-height: 1.25rem;
  font-style: normal;
  margin: 20px 0;
}

.octagon-wrapper .octagon {
  width: 104px;
  height: 104px;
  margin: 20px auto;
  display: block;
}

.octagon-wrapper .octagon svg:not(:root) {
  overflow: hidden;
}

.octagon-wrapper .octagon svg {
  width: 104px;
  height: 104px;
  transform-origin: 0 52px;
  animation: throbberRotateX 1.5s infinite cubic-bezier(0.8, 0, 0, 0.8);
}

.octagon-wrapper .octagon svg .octagon-animate {
  transform-origin: 46px 50px;
  animation: throbberRotateZ 1.5s infinite linear, throbberColor 6s infinite cubic-bezier(0.8, 0, 0, 0.8);
}

@keyframes throbberColor {
  0%, 100% {
    fill: #0ad;
  }

  25% {
    fill: #00cb3c;
  }

  50% {
    fill: #d71956;
  }

  75% {
    fill: #ff9700;
  }
}

@keyframes throbberRotateX {
  0% {
    transform: rotateX(0);
  }

  100% {
    transform: rotateX(180deg);
  }
}

@keyframes throbberRotateZ {
  0% {
    transform: rotateZ(0);
  }

  25% {
    transform: rotateZ(90deg);
  }

  50% {
    transform: rotateZ(180deg);
  }

  75% {
    transform: rotateZ(90deg);
  }

  100% {
    transform: rotateZ(0);
  }
}




