.image-editor {
  display: flex;
  flex-direction: column; }

.image-preview {
  position: relative; }

.add-layer .checkboxes {
  margin-top: 20px; }

.add-layer {
  position: relative;
  margin-bottom: 0; }

.image-preview img {
  object-fit: fill;
  width: 100%;
  height: 100%; }

.image-preview svg {
  position: absolute;
  left: 0;
  top: 0;
  cursor: pointer;
  overflow: visible;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.6);
  border: 1px solid #ddd;
  user-select: none;
  background-color: transparent;
  z-index: 1; }

.svg-text-comment {
  border: 1px solid #0063B0;
  background: rgba(210, 237, 255, 0.5);
  margin: 0;
  padding: 5px 10px;
  font-size: 20px;
  text-align: center;
  display: flex;
  max-width: 300px;
  word-break: break-all; }

.svg-flex {
  display: flex;
  flex-direction: column;
  align-items: center; }

.svg-text-delete {
  display: inline-block;
  align-self: center;
  height: 20px; }

.svg-text-delete svg {
  position: static;
  z-index: 2;
  width: 20px;
  height: 20px;
  margin: -2px 0 0 10px;
  box-shadow: none;
  border: none;
  background-color: transparent; }

.arrow-bottom-svg {
  width: 0;
  height: 0;
  border: 6px solid transparent;
  border-bottom-color: #0063B0;
  border-top: 0; }

.image-preview svg * {
  cursor: pointer; }

.btn-alert-wrapper {
  display: flex;
  justify-content: space-between; }



